@use '../../_common/'as *;

.link-list-a {
  margin-top: 12px;

  >ul {
    >li {
      position: relative;
      font-size: 1.6rem;
      font-weight: $font-regular;
      padding-left: 18px;
      margin-top: 8px;

      &::before {
        @include pseudo();
        top: 52%;
        left: 0;
        width: 10px;
        height: 10px;
        border-radius: 1000px;
        background: $bright-color-12;
        transform: translate(0, -50%);
      }
    }
  }

  @include rwd(bpSp, below) {
    margin-top: 8px;

    >ul {
      >li {
        font-size: 1.4rem;
      }
    }
  }
}

.link-list-b {
  margin-top: 10px;

  >ul {
    >li {
      font-size: 1.4rem;
    }
  }

  @include rwd(bpSp, below) {
    margin-top: 12px;
  }
}

.list-desc-achivement-a {
  >dt {
    font-weight: $font-bold;
  }

  >dd {
    font-weight: $font-regular;
  }

  @include rwd(bpSp) {
    >dt {
      font-size: 1.6rem;
    }

    >dd {
      font-size: 1.6rem;

      +dt {
        margin-top: 24px;
      }
    }
  }

  @include rwd(bpSp, below) {
    >dd {
      margin-top: 6px;

      +dt {
        margin-top: 16px;
      }
    }
  }
}

.list-desc-achivement-b {
  @extend .list-desc-achivement-a;

  @include rwd(bpSp) {
    >dt {
      +dd {
        margin-top: 4px;
      }
    }

    >dd {
      +dt {
        margin-top: 16px;
      }
    }
  }
}

.list-step-a {
  background-color: $bright-color-03;
  border-radius: 20px;

  @include rwd(bpSp, below) {
    margin: 70px 0 30px;
  }

  @include rwd(bpSp) {
    margin: 80px 0;
  }

  >ol {
    @include rwd(bpSp, below) {
      padding: 40px 20px;
    }

    @include rwd(bpSp) {
      padding: 65px 48px 65px 88px;
    }

    >li {
      position: relative;
      background-color: $bright-color-01;
      border: 1px solid $strong-color-01;
      border-radius: 10px;

      @include rwd(bpSp, below) {
        padding: 20px;
      }

      @include rwd(bpSp) {
        padding: 40px;
        padding-left: 150px;
      }

      +li {
        @include rwd(bpSp, below) {
          margin-top: 40px;
        }

        @include rwd(bpSp) {
          margin-top: 24px;
        }

        &::before {
          @include pseudo();
          width: 1px;
          background-color: $strong-color-01;
          left: 50%;
          margin-left: -1px;

          @include rwd(bpSp, below) {
            height: 40px;
            top: -41px
          }

          @include rwd(bpSp) {
            height: 24px;
            top: -25px;
          }
        }
      }

      >.hdg {
        margin-top: 0;

        @include rwd(bpSp, below) {
          text-align: center;
          margin: 15px 0;
        }

        >b {
          position: relative;
          display: block;

          >.label {
            position: absolute;
            display: inline-block;
            text-align: center;
            color: $bright-color-01;
            background-color: $strong-color-01;
            line-height: 1.6;

            @include rwd(bpSp, below) {
              font-size: 1.4rem;
              padding: 5px 10px;
              border-radius: 4px;
              min-width: 100px;
              max-width: 100%;
              top: -55px;
              left: 50%;
              transform: translateX(-50%);
            }

            @include rwd(bpSp) {
              font-size: 1.6rem;
              padding: 13px;
              border-radius: 10px;
              width: 144px;
              top: -13px;
              left: -190px;
            }
          }

          >.txt {
            display: block;
            color: $strong-color-01;

            @include rwd(bpSp, below) {
              font-size: 1.6rem;
              line-height: 1.6;
            }

            @include rwd(bpSp) {
              font-size: 2rem;
              line-height: 1.3;
            }
          }
        }
      }

      >.contents {
        @include rwd(bpSp, below) {
          margin-top: 16px;
        }

        @include rwd(bpSp) {
          margin-top: 12px;
        }

        >* {
          &:first-child {
            margin-top: 0;
          }
        }
      }
    }
  }
}

.list-bullet-a {
  >ul {
    >li {
      position: relative;
      font-weight: $font-regular;
      padding-left: 18px;

      &::before {
        @include pseudo();
        top: 13px;
        left: 0;
        width: 10px;
        height: 10px;
        border-radius: 1000px;
        background: $bright-color-12;
        transform: translate(0, -50%);
      }

      +li {
        margin-top: 8px;
      }

      >.ttl {
        font-weight: $font-bold;
      }
    }
  }

  @include rwd(bpSp, above) {
    margin-top: 12px;

    >ul {
      >li {
        font-size: 1.6rem;
      }
    }
  }

  @include rwd(bpSp, below) {
    margin-top: 8px;

    >ul {
      >li {
        font-size: 1.4rem;
      }
    }

    .lyt-history & {
      >ul {
        >li {
          &::before {
            top: 11px;
          }
        }
      }
    }
  }
}

.list-notes-a {
  margin-top: 10px;

  >ul {
    >li {
      font-size: 1.4rem;
    }
  }

  @include rwd(bpSp, below) {
    margin-top: 12px;
  }
}

.list-desc-a {
  margin-top: 24px;

  >dt {
    font-weight: $font-bold;
  }

  >dd {
    font-weight: $font-regular;
  }

  @include rwd(bpSp) {
    >dt {
      font-size: 1.6rem;

      +dd {
        margin-top: 4px;
      }
    }

    >dd {
      font-size: 1.6rem;

      +dt {
        margin-top: 16px;
      }
    }

    &._type-a {
      >dt {
        +dd {
          margin-top: 4px;
        }
      }

      >dd {
        +dt {
          margin-top: 24px;
        }
      }
    }
  }

  @include rwd(bpSp, below) {
    >dd {
      margin-top: 6px;

      +dt {
        margin-top: 16px;
      }
    }
  }
}

.list-history {
  >ol {
    >li {
      position: relative;

      &:nth-of-type(odd) {
        background: $bright-color-03;
      }

      &:nth-of-type(even) {
        background: $bright-color-01;
      }

      >.history-year {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: $font-family-02;
        font-weight: $font-semi-bold;
        color: $bright-color-01;
        background: $strong-color-01;
        transform: translate(0, -50%);
      }
    }
  }

  @include rwd(bpSp) {
    >ol {
      padding-bottom: 70px;

      >li {
        display: flex;
        align-items: center;
        min-height: 150px;
        padding: 40px 40px 40px 90px;
        border-radius: 0 20px 20px 0;

        >.history-year {
          width: 104px;
          height: 104px;
          top: 50%;
          left: -53px;
          font-size: 2.4rem;
          border-radius: 1000px;
        }
      }
    }
  }

  @include rwd(bpSp, below) {
    >ol {
      >li {
        padding: 44px 20px 40px 20px;

        &:nth-of-type(odd) {
          background: $bright-color-03;
        }

        &:nth-of-type(even) {
          padding: 84px 20px 70px 20px;

          >.history-year {
            top: 40px;
          }
        }

        >.history-year {
          width: 100px;
          height: 40px;
          top: 0;
          left: 0;
          font-size: 2rem;
          text-align: center;
          border-radius: 0 20px 20px 0;
        }
      }
    }
  }
}

.list-desc-history {
  >dl {
    >dt {
      font-weight: $font-bold;
    }

    >dd {
      font-weight: $font-regular;
    }
  }

  @include rwd(bpSp) {
    >dl {

      >dt,
      >dd {
        font-size: 1.6rem;
      }

      >dd {
        margin-top: 8px;
      }
    }
  }

  @include rwd(bpSp, below) {
    >dl {

      >dt,
      >dd {
        font-size: 1.4rem;
      }

      >dd {
        margin-top: 16px;
      }
    }
  }
}

.list-interview {
  >ul {
    >li {
      >.list-interview-item {
        position: relative;

        >.txt-person-join {
          position: absolute;
          left: 0;
          top: 20px;
          display: inline-block;
          width: 100%;
          padding: 7px;
          border-radius: 1000px;
          background: ;
          color: $bright-color-01;
          text-align: center;
          font-weight: $font-bold;
          z-index: 2;
        }

        >a {
          display: block;
          overflow: hidden;
          border-radius: 30px;
          padding-left: 20px;

          @include hover() {
            >.view-content {
              >.arrow {
                &::before {
                  transition: transform .4s cubic-bezier(.23, 1, .32, 1) 0s, opacity .3s cubic-bezier(.23, 1, .32, 1) 0s;
                  opacity: 0;
                  transform: translateX(300%);
                }

                &::after {
                  transition: transform .4s cubic-bezier(.23, 1, .32, 1) .1s;
                  transform: translateX(0);
                }
              }
            }
          }

          >.view-content {
            position: relative;
            border: solid 1px $strong-color-01;
            border-radius: 30px;

            &::before {
              @include pseudo();
              top: -52px;
              right: -44px;
              width: 170px;
              height: 201px;
              background: url('/docs/company/recruit/graduates/common/images/decoration-person-01.svg') no-repeat top left / cover;
              z-index: -1;
            }

            &::after {
              @include pseudo();
              bottom: 0;
              left: 0;
              background: $strong-color-03;
              z-index: -1;
              border-radius: 0 1000px 1000px 300px;
            }

            >.view-item {
              >img {
                width: auto;
                transform: translateX(-30px);
              }
            }

            >.arrow {
              position: absolute;
              bottom: 10px;
              right: 10px;
              display: inline-block;
              width: 32px;
              height: 32px;
              border-radius: 1000px;
              background: $gradient-color-01;
              overflow: hidden;

              &::before,
              &::after {
                @include pseudo();
                top: 14px;
                right: 12px;
                width: 8px;
                height: 5px;
                background: url('/docs/company/recruit/graduates/common/images/icon-arrow-03.svg') center / cover;
              }

              &::after {
                transform: translateX(-300%);
              }
            }
          }
        }

        >.txt-person-name {
          font-weight: $font-medium;
        }
      }
    }
  }

  @include rwd(425px, above) {
    margin-top: 80px;

    >ul {
      display: flex;
      flex-wrap: wrap;
      margin-left: -60px;
      margin-bottom: -64px;

      >li {
        width: calc(100% / 2);
        padding-left: 60px;
        padding-bottom: 64px;

        >.list-interview-item {
          >.txt-person-join {
            max-width: 140px;
            font-size: 1.4rem;
          }

          >a {
            max-width: 320px;

            >.view-content {
              height: calc(507vw * 100 / 768);
              min-height: 260px;
              max-height: 507px;

              &::after {
                width: calc(200vw * 100 / 768);
                min-width: 100px;
                max-width: 200px;
                height: calc(200vw * 100 / 768);
                min-height: 100px;
                max-height: 200px;
              }

              >.view-item {
                >img {
                  width: calc(390vw * 100 / 768);
                  min-width: 230px;
                  max-width: 390px;
                }
              }
            }
          }

          >.txt-person-career {
            margin-top: 24px;
            padding-left: 20px;
            font-size: 1.4rem;
          }

          >.txt-person-name {
            margin-top: 10px;
            padding-left: 20px;
            font-size: 2rem;
          }
        }
      }
    }
  }

  @include rwd(bpSp, above) {
    >ul {
      >li {
        width: calc(100% / 3);

        >.list-interview-item {
          >.txt-person-join {
            font-size: calc(1.4rem + (18 - 14) * (100vw - 320px) / (1440 - 768));
          }

          >a {
            max-width: 320px;

            >.view-content {
              height: calc(507vw * 100 / 1440);
              min-height: 260px;
              max-height: 507px;

              &::after {
                width: calc(200vw * 100 / 1440);
                min-width: 100px;
                max-width: 200px;
                height: calc(200vw * 100 / 1440);
                min-height: 100px;
                max-height: 200px;
              }

              >.view-item {
                >img {
                  width: calc(390vw * 100 / 1440);
                  min-width: 230px;
                  max-width: 390px;
                }
              }
            }
          }
        }
      }
    }
  }

  @include rwd(bpTab, above) {
    >ul {
      >li {
        >.list-interview-item {
          >.txt-person-join {
            font-size: 1.8rem;
          }

          >.txt-person-name {
            font-size: 2.8rem;
          }

          >.txt-person-career {
            font-size: 1.6rem;
          }
        }
      }
    }
  }

  @include rwd(425px, below) {
    margin-top: 32px;

    >ul {
      >li {
        max-width: 295px;
        width: 100%;
        margin: 32px auto 0;

        >.list-interview-item {
          >.txt-person-join {
            max-width: 128px;
            font-size: 1.4rem;
          }

          >a {
            >.view-content {
              height: 467px;

              &::after {
                width: 200px;
                height: 200px;
              }

              >.view-item {
                >img {
                  width: 120%;
                }
              }
            }
          }

          >.txt-person-career {
            margin-top: 16px;
            padding-left: 20px;
            font-size: 1.4rem;
          }

          >.txt-person-name {
            margin-top: 8px;
            padding-left: 20px;
            font-size: 2rem;
          }
        }
      }
    }
  }
}

