.list-business-a {
  >ul {
    >li {
      position: relative;
      overflow: hidden;
      border-radius: 20px;
      background: $bright-color-03;
      margin-top: 24px;
      z-index: 2;

      >a {
        text-decoration: none;

        >.item {
          >.img {
            position: relative;

            &::before {
              @include pseudo();
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              background: rgba($color: $dark-color-07, $alpha: .4);
              border-radius: 20px 0 0 0;
              z-index: 1;
            }

            >img {
              filter: blur(2px);
              height: 100%;
              object-fit: cover;
            }

            >.title {
              position: absolute;
              top: 50%;
              left: 50%;
              width: 100%;
              margin-top: 0;
              transform: translate(-50%, -50%);
              text-align: center;
              color: $bright-color-01;
              z-index: 2;
            }
          }

          >.txt {
            >p {
              margin-top: 0;
            }
          }
        }
      }

      >.business-items {
        position: relative;
        z-index: 1;

        .title-content {
          position: relative;
          overflow: hidden;

          &._in-tab {
            >.txt {
              top: 40%;
            }
          }

          >.txt {
            position: absolute;
            top: 50%;
            left: 50%;
            width: calc(100% - 20%);
            text-align: center;
            transform: translate(-50%, -50%);
            color: $bright-color-01;
            word-break: break-all;
            z-index: 2;

            >.business-title {
              border-bottom: solid 1px $bright-color-01;
            }
          }

          >.img {
            position: relative;

            &::before {
              @include pseudo();
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              background: rgba($color: $dark-color-07, $alpha: .4);
              z-index: 1;
            }

            >img {
              filter: blur(2px);
            }
