              font-size: 1.8rem;
              font-weight: $font-bold;
            }

            >.txt {
              width: 100%;
              border-radius: 0 20px 20px 0;
              background: $bright-color-03;
              padding: 40px;

              >p {
                color: ;
              }
            }
          }
        }

        >.business-items {
          border-radius: 20px;

          .title-content {
            position: relative;
            max-height: 276px;
            height: 100%;

            >.txt {
              >.business-title {
                padding-bottom: 33px;
                font-size: 3.2rem;

                +p {
                  padding-top: 25px;
                  margin-top: 0;
                  font-size: 2rem;
                }
              }
            }

            >.img {
              >img {
                transform: translateY(-60px);
              }
            }
          }

          .business-content {
            padding: 64px 128px 92px;
          }
        }
      }
    }
  }

  @include rwd(bpSp, below) {
    >ul {
      >li {
        >a {
          >.item {
            display: none;

            [data-script-enabled="true"] & {
              display: block;
