              >.title {
                width: calc(100% - 40px);
                top: 25%;
                padding-bottom: 9px;
                border-bottom: solid 1px $bright-color-01;
                font-size: 2rem;
                font-weight: $font-bold;
              }

              >img {
                min-height: 232px;
              }
            }

            >.txt {
              position: absolute;
              top: 60%;
              left: 50%;
              width: calc(100% - 40px);
              transform: translate(-50%, -50%);
              color: $bright-color-01;
              z-index: 2;
              font-size: 1.4rem;
            }
          }
        }

        >.business-items {
          .business-content {
            padding: 24px 20px 77px;
          }

          .title-content {
            >.txt {
              >.business-title {
                padding-bottom: 9px;
                font-size: 2rem;

                +p {
                  padding-top: 8px;
                  margin-top: 0;
                  font-size: 1.4rem;
                }
              }
            }

            >.img {
              >img {
                min-height: 200px;
                min-width: 508px;
              }
            }
          }
        }
      }
    }
  }
}

