.list-bullet-b {
  >ul {
    >li {
      position: relative;
      display: block;
      padding: 14px 8px 14px 32px;
      border-radius: 10px;
      background: $bright-color-03;

      &::before {
        @include pseudo();
        top: 50%;
        left: 14px;
        width: 10px;
        height: 10px;
        border-radius: 1000px;
        background: $bright-color-12;
        transform: translate(0, -50%);
      }
    }
  }

  @include rwd(bpSp) {
    margin-top: 24px;

    >ul {
      display: flex;
      flex-wrap: wrap;
      margin-left: -6px;
      margin-bottom: -6px;

      >li {
        width: 211px;
        margin-left: 6px;
        margin-bottom: 6px;
      }
    }
  }

  @include rwd(bpSp, below) {
    margin-top: 16px;

    >ul {
      >li {
        width: 100%;
        margin-top: 4px;

        &:first-of-type {
          margin-top: 0;
        }
      }
    }
  }
}

.list-career-a {
  >ul {
    >li {
      position: relative;
      display: flex;

      &::before {
        background: $bright-color-11;
      }

      >.txt {
        >* {
          &:first-of-type {
            margin-top: 0;
          }
        }

        >.ttl {
          font-weight: $font-bold;
          color: $strong-color-01;
        }
      }

      >.img {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-shrink: 0;
        border-radius: 1000px;
        background: $bright-color-03;
      }
    }
  }

  @include rwd(bpSp) {
    margin-top: 32px;

    >ul {
      display: flex;
      flex-wrap: wrap;
      margin-left: -48px;
      margin-bottom: -22px;

      >li {
        width: calc((100% / 2) - 48px);
        margin-left: 48px;
        margin-bottom: 22px;

        &:nth-of-type(even) {
          &::before {
            @include pseudo;
            top: 0;
            left: -24px;
            width: 1px;
            height: 100%;
          }
        }

        >.txt {
          margin-left: 24px;

          >.ttl {
            font-size: 1.8rem;
          }
        }

        >.img {
          width: 110px;
          height: 110px;
        }
      }
    }
  }

  @include rwd(bpSp, below) {
    margin-top: 24px;

    >ul {
      margin-top: 24px;

      >li {
        padding: 24px 0;

        &::before {
          @include pseudo;
          top: 0;
          left: 0;
          width: 100%;
          height: 1px;
        }

        >.txt {
          margin-left: 16px;

          >.ttl {
            font-size: 1.6rem;
          }
        }

        >.img {
          width: 64px;
          height: 64px;
        }
      }
    }
  }
}

.list-feature-a {
  margin-top: 32px;

  &._type-a {
    >ul {
      display: flex;
    }
  }

  >ul {
    >li {
      >div {
        >.img {
          display: flex;
          justify-content: center;
          align-items: center;
          position: relative;
          margin: 0 auto;
          border-radius: 1000px;
          background: $bright-color-01;
        }

        .txt {
          text-align: center;
        }
      }
    }
  }

  @include rwd(bpSp, below) {
    &._type-a {
      >ul {
        flex-wrap: wrap;
        margin-right: -31px;
        margin-bottom: -24px;

        >li {
          width: calc((100% / 2) - 31px);
          margin-right: 31px;
          margin-bottom: 24px;
          border: none;

          >div {
            >.img {
              max-width: 120px;
              min-width: 90px;
              width: calc(120vw * 100 / 880);
              max-height: 120px;
              min-height: 90px;
              height: calc(120vw * 100 / 880);
            }

            .txt {
              margin-top: 9px;
            }
          }
        }
      }
    }

    >ul {
      >li {
        padding: 24px 0;
        border-top: solid 1px $bright-color-11;

        &:first-of-type {
          border-top: 0;
        }

        >div {
          >.img {
            width: 128px;
            height: 128px;
          }

          .txt {
            margin-top: 18px;
          }
        }
      }
    }
  }

  @include rwd(bpSp, above) {
    &._type-a {
      >ul {
        justify-content: center;

        >li {
          width: auto;
          margin-right: 48px;
          border: none;

          &:last-of-type {
            margin-right: 0;
          }

          >div {
            >.img {
              max-width: 140px;
              min-width: 90px;
              width: calc(120vw * 100 / 880);
              max-height: 140px;
              min-height: 90px;
              height: calc(120vw * 100 / 880);
            }

            .txt {
              margin-top: 18px;
            }
          }
        }
      }
    }

    >ul {
      display: flex;
      justify-content: center;

      >li {
        width: calc(100% / 3);
        padding: 0 12px;
        border-left: solid 1px $bright-color-11;

        &:first-of-type {
          border-left: 0;
        }

        >div {
          >.img {
            max-width: 128px;
            min-width: 90px;
            width: calc(120vw * 100 / 880);
            max-height: 128px;
            min-height: 90px;
            height: calc(120vw * 100 / 880);
          }

          .txt {
            margin-top: 26px;
          }
        }
      }
    }
  }
}

.list-member-a {
  >ul {
    >li {
      >div {
        display: flex;
        align-items: center;

        >.img {
          flex-shrink: 0;

          >img {
            border-radius: 1000px;
          }
        }

        >.txt-wrap {
          word-break: break-word;

          >.txt {
            .detail {
              display: block;
            }
          }
        }
      }
    }
  }

  @include rwd(bpSp, above) {
    margin-top: 50px;

    >ul {
      display: flex;
      flex-wrap: wrap;
      margin-left: -50px;
      margin-bottom: -20px;

      >li {
        position: relative;
        width: calc((100% / 2) - 50px);
        margin-left: 50px;
        margin-bottom: 20px;
        padding-right: 50px;

        &:nth-of-type(even) {
          &::before {
            @include pseudo();
            top: 0;
            left: -50px;
            width: 1px;
            height: 100%;
            background: $bright-color-09;
          }
        }

        >div {
          >.img {
            width: 120px;
          }

          >.txt-wrap {
            margin-left: 32px;

            >.txt {
              font-size: 2rem;

              .detail {
                font-size: 1.6rem;
              }
            }
          }
        }
      }
    }
  }

  @include rwd(bpTab, above) {
    >ul {
      >li {
        width: calc((100% / 3) - 50px);

        &:nth-of-type(n+2) {
          &::before {
            @include pseudo();
            top: 0;
            left: -50px;
            width: 1px;
            height: 100%;
            background: $bright-color-09;
          }
        }
      }
    }
  }

  @include rwd(bpSp, below) {
    margin-top: 25px;

    >ul {
      >li {
        padding: 16px 0;

        &:first-of-type {
          padding-top: 0;
        }

        &:last-of-type {
          padding-bottom: 0;
        }

        &:nth-of-type(n+2) {
          border-top: solid 1px $bright-color-09;
        }

        >div {
          >.img {
            width: 80px;

            >img {
              border-radius: 1000px;
            }
          }

          >.txt-wrap {
            margin-left: 22px;

            >.txt {
              font-size: 1.6rem;

              .detail {
                font-size: 1.4rem;
              }
            }
          }
        }
      }
    }
  }
}

.list-challenge-a {
  >ul {
    >li {
      &:nth-of-type(odd) {
        >div {
          background: $bright-color-01;

          >.txt {
            >.circle {
              &::before {
                background: $bright-color-01;
              }
            }
          }
        }
      }

      &:nth-of-type(even) {
        >div {
          background: $bright-color-03;

          >.txt {
            >.circle {
              &::before {
                background: $bright-color-03;
              }
            }
          }
        }
      }

      >div {
        position: relative;
        padding: 0 20px;

        >.txt {
          >.circle {
            position: absolute;
            left: 50%;
            transform: translate(-50%, 0);
            display: flex;
            justify-content: center;
            align-items: center;

            &::before {
              @include pseudo();
              top: 50%;
              left: 50%;
              border-radius: 1000px;
              transform: translate(-50%, -50%);
              z-index: -1;
            }

            &._type-a {
              >.num {
                background: $challenge-color-01;
              }
            }

            &._type-b {
              >.num {
                background: $challenge-color-02;
              }
            }

            &._type-c {
              >.num {
                background: $challenge-color-03;
              }
            }

            &._type-d {
              >.num {
                background: $challenge-color-04;
              }
            }

            &._type-e {
              >.num {
                background: $challenge-color-05;
              }
            }

            &._type-f {
              >.num {
                background: $challenge-color-06;
              }
            }

            >.num {
              display: flex;
              justify-content: center;
              align-items: center;
              border-radius: 1000px;
              color: $bright-color-01;
            }
          }

          >.ttl {
            position: relative;
            text-align: center;

            &::before {
              @include pseudo();
              bottom: 0;
              left: 50%;
              height: 1px;
              transform: translate(-50%, 0);
              background: $bright-color-11;
            }
          }
        }

        >.img {
          border-radius: 30px;
          overflow: hidden;
          margin: 40px auto 0;
        }
      }
    }
  }

  @include rwd(bpSp, above) {
    >ul {
      >li {
        >div {
          padding-top: 114px;
          padding-bottom: 120px;

          >.txt {
            text-align: center;

            >.circle {
              top: -40px;
              width: 152px;
              height: 152px;

              &::before {
                width: 100%;
                height: 100%;
              }

              >.num {
                width: 72px;
                height: 72px;
              }
            }

            >.ttl {
              font-size: 3.2rem;
              padding-bottom: 25px;

              &::before {
                width: 48px;
              }

              &+.hdg-c {
                margin-top: 26px;
              }
            }
          }

          >.img {
            max-width: 780px;
          }
        }
      }
    }
  }

  @include rwd(bpSp, below) {
    >ul {
      >li {
        >div {
          padding-top: 65px;
          padding-bottom: 48px;

          >.txt {
            >.circle {
              top: -16px;
              width: 72px;
              height: 72px;

              &::before {
                width: 100%;
                height: 100%;
              }

              >.num {
                width: 40px;
                height: 40px;
              }
            }

            >.ttl {
              font-size: 2rem;
              padding-bottom: 17px;

              &::before {
                width: 32px;
              }

              &+.hdg-c {
                font-size: 1.6rem;
                margin-top: 18px;
              }
            }

            >p {
              font-size: 1.4rem;
            }
          }

          >.img {
            max-width: 335px;
          }
        }
      }
    }
  }
}
