@use '../../_common/'as *;

.box-tbl-a {
  @include rwd(bpSp) {
    padding: 10px 10px 10px 32px;
    border-radius: 20px;
    background: $bright-color-03;
  }
}

.tbl-a {
  width: 100%;

  tbody {
    th,
    td {
      vertical-align: top;
      text-align: left;
    }

    th {
      font-size: 1.6rem;
      font-weight: $font-medium;
      color: $dark-color-03;
    }

    td {
      > * {
        &:first-child {
          margin-top: 0 !important;
        }
      }
    }
  }

  @include rwd(bpSp) {
    tbody {
      tr {
        position: relative;

        &::after {
          position: absolute;
          content: '';
          bottom: 0;
          left: 0;
          width: calc(100% - 32px);
          height: 1px;
          border-bottom: dashed 2px $bright-color-11;
        }

        &:first-of-type {
          td {
            border-radius: 12px 12px 0 0;
          }
        }

        &:last-of-type {
          td {
            border-radius: 0 0 12px 12px;
          }

          &::after {
            display: none;
          }
        }
      }

      th {
        position: relative;
        padding: 32px 32px 32px 0;
      }

      td {
        background: $bright-color-01;
        padding: 32px;
      }
    }
  }

  @include rwd(bpSp, below) {
    colgroup {
      display: none;
    }

    tbody {

      th,
      td {
        display: block;
      }

      th {
        padding: 10.5px;
        background: $bright-color-10;
        border-radius: 10px;
        text-align: center;
      }

      td {
        background: #fff;
        padding: 16px 0 24px;
        font-size: 1.4rem;
      }
    }
  }
}

@for $value from 2 through 90 {
  .w-#{$value} {
    width: 0% + $value;
  }
}
