@use '../_common/'as *;

header {
  background: $bright-color-01;
  z-index: 100;

  [data-script-enabled="true"] & {
    position: sticky;
    top: 0;
    left: 0;
  }
}

.header-contents {
  padding: 16px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  [data-script-enabled="true"] & {
    .header-global-nav {
      .header-menu-contents {
        overflow: hidden;
        transition: height .3s ease-in-out;

        >ul {
          >li {
            >a {
              color: ;
              text-decoration: none;

              >.txt {
                font-weight: $font-medium;
                font-size: 1.6rem;
              }
            }
          }
        }
      }
    }
  }
}

.header-logo-area {
  position: relative;
  display: inline-block;

  >.txt {
    display: block;
    font-size: .8rem;
    font-weight: $font-medium;
    color: $dark-color-03;
    text-align: center;
    margin-top: 8.2px;
  }
}

.header-menu-area {
  .header-global-nav {
    >li {
      >.nav-list-contents {
        >a {
          text-decoration: none;
          position: relative;
          display: inline-block;

          >.txt {
            font-weight: $font-medium;
            color: ;
          }
        }

        &._no-icon {
          >a {
            >.txt {
              padding-right: 0;
            }
          }
        }
      }
    }
  }
}

@include rwd(bpTab, below) {
  .header-contents {
    display: block;
    align-items: flex-start;

    [data-script-enabled="true"] & {
      display: flex;
    }

    .header-menu {
      .sp-header-menu {

        &:not(.is-active) {
          +.header-nav-area {
            .header-global-nav {
              .nav-list-contents {
                >button {
                  &::before,
                  &::after {
                    @include pseudo();
                    width: 8px;
                    height: 2px;
                    right: 13px;
                    border-radius: 20px;
                    background: $gradient-color-01;
                  }

                  &::before {
                    top: 48%;
                    transform: rotate(40deg);
                  }

                  &::after {
                    top: 56%;
                    transform: rotate(-40deg);
                  }
                }
              }
            }
          }
        }

        +.header-nav-area {
          position: relative;
          display: block;
          overflow: scroll;
          width: 100%;
          padding-top: 30px;
          z-index: 2;

          .header-global-nav {
            display: block;
            padding: 0 20px;
          }
        }
      }

      .header-global-nav {
        .nav-list-contents {
          >a {
            display: block;
            padding: 16px 10px 16px 16px;
            border-bottom: solid 1px $bright-color-02;

            +.header-menu-contents {
              >.header-menu-title {
                display: none;
              }

              >ul {
                >li {
                  &:last-of-type {
                    >a {
                      border-radius: 0 0 10px 10px;
                    }
                  }

                  >.header-menu-under {
                    .header-business-detail {
                      >li {
                        >a {
                          padding: 16px 13px 16px 32px;
                        }
                      }
                    }
                  }

                  a {
                    position: relative;
                    display: block;
                    color: ;
                    background: $bright-color-03;
                    border-bottom: solid 1px $bright-color-01;
                    text-decoration: none;
                    padding: 16px 13px 16px 16px;

                    &::before,
                    &::after {
                      @include pseudo();
                      width: 8px;
                      height: 2px;
                      right: 13px;
                      border-radius: 1px;
                      background: $gradient-color-01;
                    }

                    &::before {
                      top: 48%;
                      transform: rotate(40deg);
                    }

                    &::after {
                      top: 56%;
                      transform: rotate(-40deg);
                    }

                    >.txt {
                      font-size: 1.4rem;
                      font-weight: $font-regular;
                    }
                  }
                }
              }
            }

            &:not(.is-active) {
              +.header-menu-contents {
                display: none;
              }
            }

            >.txt {
              position: relative;
              font-size: 1.4rem;
            }
          }
        }
      }
    }

    [data-script-enabled="true"] & {
      .header-menu {
        .sp-header-menu {
          +.header-nav-area {
            position: relative;
            display: none;
            opacity: 0;
            height: calc(100vh - 70px);
            position: absolute;
            top: 70px;
            left: 0;
            padding-top: 0;
            z-index: 2;
          }

          &.is-active {
            +.header-nav-area {
              display: block;
              animation: menuFadeInAnimation .3s .3s ease-in-out forwards;
              height: calc(100vh - 160px);
              overflow: scroll;
            }
          }

          &.is-close {
            +.header-nav-area {
              display: block;
              animation: menuCloseFadeInAnimation .3s ease-in-out forwards;
            }
          }
        }

        .header-global-nav {
          .nav-list-contents {
            &._no-icon {
              >a {
                padding: 16px 13px 16px 16px;

                &::before,
                &::after {
                  @include pseudo();
                  width: 8px;
                  height: 2px;
                  right: 13px;
                  border-radius: 20px;
                  background: $gradient-color-01;
                }

                &::before {
                  top: 48%;
                  transform: rotate(40deg);
                }

                &::after {
                  top: 56%;
                  transform: rotate(-40deg);
                }
              }
            }

            >a {
              &.is-active {
                &::after {
                  transform: translateY(-40%) rotate(90deg);
                }

                +.header-menu-contents {
                  display: block;
                }
              }

              &::before,
              &::after {
                @include pseudo();
                border-radius: 1px;
              }

              &::before {
                top: 50%;
                right: 10px;
                width: 14px;
                height: 2px;
                background: $gradient-color-01;
              }

              &::after {
                top: 50%;
                right: 16px;
                width: 2px;
                height: 14px;
                background: $gradient-color-01;
                transform: translateY(-40%);
                transition: .3s;
              }
            }
          }
        }
      }
    }
  }

  .header-menu-area {
    display: flex;
    flex-direction: column-reverse;
    margin-top: -60px;

    [data-script-enabled="true"] & {
      flex-direction: row-reverse;
      margin-top: 0;

      .sp-header-menu {
        display: block;
      }
    }

    .header-global-nav {
      >li {
        >.nav-list-contents {
          &._no-icon {
            >a {
              >.txt {
                padding-right: 6px;
              }
            }
          }
        }
      }
    }

    .sp-header-menu {
      display: none;

      &.is-active {
        >button {
          &::before {
            top: 22px;
            left: 45%;
            transform: rotate(45deg) translateX(-50%);
          }

          &::after {
            bottom: 23px;
            left: 44%;
            transform: rotate(-47deg) translateX(-50%);
          }
        }
      }

      >button {
        position: relative;
        display: block;
        width: 36px;
        height: 36px;
        background: $dark-color-05;
        border-radius: 10000px;
        border: none;
        cursor: pointer;

        &::before,
        &::after {
          @include pseudo();
          width: 14px;
          height: 1px;
          left: 50%;
          background: $bright-color-01;
          transform: translateX(-50%);
          transition: .3s;
        }

        &::before {
          top: 15px;
        }

        &::after {
          bottom: 15px;
        }

        >.txt {
          @include pseudo();
          left: 50%;
          bottom: -24px;
          width: 44px;
          transform: translate(-50%, -50%);
          font-family: $font-family-02;
          font-weight: $font-semi-bold;
          font-size: 1rem;
          text-align: center;
        }
      }
    }
  }
}

@include rwd(bpTab) {
  .header-logo-area {
    display: inline-block;
  }

  .header-menu-area {
    display: flex;
    justify-content: end;
    align-items: center;

    .sp-header-menu {
      display: none;
    }

    [data-script-enabled="true"] & {
      .header-global-nav {
        >li {
          >.nav-list-contents {
            >a {
              >.txt {

                &::before,
                &::after {
                  @include pseudo();
                  width: 6px;
                  height: 2px;
                  top: 50%;
                  border-radius: 1px;
                  background: $strong-color-01;
                  transition: .4s;
                }

                &::before {
                  right: 3px;
                  transform: rotate(50deg);
                }

                &::after {
                  right: 0;
                  transform: rotate(-50deg);
                }
              }
            }
          }
        }
      }
    }

    .header-global-nav {
      display: flex;
      margin-right: 21px;

      >li {
        padding-right: 20px;

        >.nav-list-contents {
          &._no-icon {
            >a {
              >.txt {
                padding-right: 0;

                &::before,
                &::after {
                  display: none;
                }
              }
            }
          }

          >a {
            &:not(.is-active) {
              +.header-menu-contents {
                display: none;
              }
            }

            +.header-menu-contents {
              display: flex;
              position: absolute;
              top: 88px;
              left: 0;
              width: 100%;

              >.header-menu-title {
                max-width: 270px;
                width: 100%;
                padding: 26px 36px 33px;
                background: $strong-color-01;

                >.title {
                  color: rgba($color: $bright-color-01, $alpha: .4);
                  font-family: $font-family-02;
                  font-size: 4rem;
                  font-weight: $font-semi-bold;
                  text-align: center;

                  >.title-detail {
                    display: block;
                    color: $bright-color-01;
                    font-family: $font-family-01;
                    font-weight: $font-medium;
                    font-size: 1.4rem;
                    text-align: center;
                  }
                }
              }

              >ul {
                display: flex;
                flex-wrap: wrap;
                width: 100%;
                padding: 32px 40px;
                margin-top: -16px;
                background: rgba($color: $bright-color-01, $alpha: .8);
                line-height: 1.8;

                >li {
                  padding-top: 16px;
                  padding-right: 24px;

                  .header-menu-under {
                    >ul {
                      >li {
                        display: flex;

                        >a {
                          flex-shrink: 0;
                          margin-right: 48px;
                        }

                        >.header-business-detail {
                          display: inline-flex;
                          flex-wrap: wrap;

                          >li {
                            margin-right: 24px;

                            &:first-of-type {
                              position: relative;

                              &::before {
                                @include pseudo();
                                left: -24px;
                                top: 4px;
                                height: calc(100% - 8px);
                                width: 1px;
                                background: $bright-color-08;
                              }
                            }

                            &:last-of-type {
                              margin-right: 0;
                            }

                            >a {
                              color: ;
                              text-decoration: none;

                              >.txt {
                                font-weight: $font-regular;
                              }
                            }
                          }
                        }

                        >a {
                          text-decoration: none;
                          color: ;
                        }
                      }
                    }
                  }

                  a {
                    @include hover() {
                      >.txt {

                        &::before,
                        &::after {
                          right: -2px;
                        }
                      }
                    }

                    >.txt {
                      position: relative;
                      padding-right: 12px;
                      font-size: 1.6rem;
                      font-weight: $font-medium;

                      &::before,
                      &::after {
                        @include pseudo();
                        width: 6px;
                        height: 2px;
                        right: 0;
                        border-radius: 1px;
                        background: $strong-color-01;
                        transition: .1s;
                      }

                      &::before {
                        top: 42%;
                        transform: rotate(50deg);
                      }

                      &::after {
                        top: 54%;
                        transform: rotate(-50deg);
                      }
                    }
                  }
                }
              }
            }

            &.is-active {
              +.header-menu-contents {
                animation: globalNavOpenAnimation .2s ease-in-out;
                z-index: 99;
              }

              >.txt {
                &::before {
                  transform: rotate(-50deg);
                }

                &::after {
                  transform: rotate(50deg);
                }
              }
            }

            &.is-closed {
              +.header-menu-contents {
                animation: globalNavCloseAnimation .2s ease-in-out forwards;
              }
            }

            >.txt {
              display: flex;
              position: relative;
              font-size: 1.4rem;
              padding-right: 12px;
              font-weight: $font-medium;
            }
          }
        }

        &:last-of-type {
          padding-right: 0;
        }
      }
    }
  }
}

@include rwd(1170px) {
  .header-contents {
    padding: 28px 40px 30px;
  }

  .header-logo-area {
    >.txt {
      font-size: 1.2rem;
    }

    >.logo {
      >a {
        >img {
          width: 100%;
        }
      }
    }
  }

  .header-menu-area {
    .header-global-nav {
      >li {
        >.nav-list-contents {
          >a {
            &.is-closed {
              +.header-menu-contents {
                top: 134px;
              }
            }

            &.is-active {
              &::after {
                transform: scale(1, 1);
              }

              +.header-menu-contents {
                top: 134px;
              }
            }

            &::after {
              position: absolute;
              bottom: -5px;
              left: 0;
              content: '';
              width: 100%;
              height: 2px;
              background: $strong-color-01;
              transform: scale(0, 1);
              transform-origin: left top;
              transition: .3s;
            }

            @include hover() {
              &::after {
                transform: scale(1, 1);
              }
            }

            >.txt {
              font-size: 1.6rem;
            }
          }
        }

        &:last-of-type {
          padding-right: 0;
        }
      }
    }
  }
}

@include rwd(1170px, below) {
  .header-logo-area {
    >.logo {
      a {
        display: block;

        >img {
          width: 96px;
        }
      }
    }
  }
}

.header-btn-list-a {
  >ul {
    display: flex;

    >li {
      >a {
        position: relative;
        display: inline-block;
        text-decoration: none;
        transition: .3s;

        @include hover() {
          opacity: .8;
        }

        >.txt {
          position: relative;
          font-size: 1rem;
        }
      }

      &.btn-mypage {
        >a {
          background: $bright-color-02;

          >.txt {
            color: $dark-color-01;
            font-family: $font-family-02;
            font-weight: $font-semi-bold;
          }
        }
      }

      &.btn-entry {
        margin-top: 0;

        >a {
          background: $gradient-color-01;

          >.txt {
            color: $dark-color-01;
            font-family: $font-family-02;
            font-weight: $font-semi-bold;
          }
        }
      }
    }
  }

  @include rwd(bpTab, below) {
    >ul {
      justify-content: flex-end;

      [data-script-enabled="true"] & {
        justify-content: flex-start;
      }

      >li {
        padding-right: 16px;

        &.btn-mypage,
        &.btn-entry {
          >a {
            >.txt {
              font-size: 1rem;
            }
          }
        }

        >a {
          width: 36px;
          height: 36px;
          border-radius: 1000px;

          >.txt {
            display: block;
            width: 45px;
            position: absolute;
            bottom: -18px;
            left: -5px;
            text-align: center;
          }

          >.img {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }

  @include rwd(bpTab) {
    >ul {
      >li {
        width: 120px;
        margin-right: 10px;

        >a {
          display: flex;
          justify-content: center;
          flex-direction: row-reverse;
          width: 100%;
          border-radius: 22px;
          padding: 12px 20px 11px 19px;

          >.txt {
            display: flex;
            flex-direction: row-reverse;
            justify-content: center;
            align-items: center;
            font-size: 1.4rem;
          }
        }

        &:last-of-type {
          margin-right: 0;
        }

        &.btn-mypage {
          >a {
            >.txt {
              padding-left: 6px;
            }
          }
        }

        &.btn-entry {
          >a {
            >.txt {
              color: $bright-color-01;
              padding-left: 8px;
            }
          }
        }
      }
    }
  }
}
