@use '../../_common/'as *;

.btn-a {
  position: relative;

  >a {
    position: relative;
    display: block;
    max-width: 170px;
    width: 100%;
    padding: 6px 32px 20px 0;
    border-bottom: solid 2px $bright-color-02;
    text-decoration: none;
    color: ;

    @include hover() {
      >.txt {
        >.arrow {
          &::before {
            transition: transform .4s cubic-bezier(.23, 1, .32, 1) 0s, opacity .3s cubic-bezier(.23, 1, .32, 1) 0s;
            opacity: 0;
            transform: translateX(300%);
          }

          &::after {
            transition: transform .4s cubic-bezier(.23, 1, .32, 1) .1s;
            transform: translateX(0);
          }
        }
      }
    }

    >.txt {
      >.arrow {
        position: absolute;
        top: 0;
        right: 0;
        display: inline-block;
        width: 32px;
        height: 32px;
        border-radius: 1000px;
        background: $gradient-color-01;
        overflow: hidden;

        &::before,
        &::after {
          @include pseudo();
          top: 14px;
          right: 12px;
          width: 8px;
          height: 5px;
          background: url('/docs/company/recruit/graduates/common/images/icon-arrow-03.svg') center / cover;
        }

        &::after {
          transform: translateX(-300%);
        }
      }
    }
  }

  @include rwd(bpSp) {
    margin-top: 24px;

    &::before {
      width: 32px;
      height: 32px;
    }
  }

  @include rwd(bpSp, below) {
    margin-top: 47px;

    >a {
      >.txt {
        font-size: 1.4rem;
      }
    }

    .box-career & {
      margin-top: 25px;
    }
  }
}

.btn-unique-a {
  >a {
    position: relative;
    display: block;
    max-width: 800px;
    width: 100%;
    margin: 0 auto;
    border-radius: 20px;
    background: $bright-color-03;
    text-decoration: none;

    @include hover() {
      >.txt {
        >.arrow {
          &::before {
            transition: transform .4s cubic-bezier(.23, 1, .32, 1) 0s, opacity .3s cubic-bezier(.23, 1, .32, 1) 0s;
            opacity: 0;
            transform: translateX(300%);
          }

          &::after {
            transition: transform .4s cubic-bezier(.23, 1, .32, 1) .1s;
            transform: translateX(0);
          }
        }
      }
    }

    >.title {
      position: absolute;
      margin-top: 0;
      text-align: center;
      transform: translate(-50%, 0);
      line-height: 1.1;

      >b {
        display: inline-block;
        background: $gradient-color-01;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        font-family: $font-family-02;
        font-weight: $font-semi-bold;
      }
    }

    >.img {
      >img {
        border-radius: 1000px;
      }
    }

    >.txt {
      color: ;

      >.txt-lead-a {
        font-weight: $font-bold;
        text-align: center;
      }

      >.arrow {
        position: absolute;
        display: inline-block;
        width: 32px;
        height: 32px;
        border-radius: 1000px;
        background: $gradient-color-01;
        overflow: hidden;

        &::before,
        &::after {
          @include pseudo();
          top: 14px;
          right: 12px;
          width: 8px;
          height: 5px;
          background: url('/docs/company/recruit/graduates/common/images/icon-arrow-03.svg') center / cover;
        }

        &::after {
          transform: translateX(-300%);
        }
      }
    }

    >.img {
      position: absolute;
      transform: translate(-50%, -50%);
    }
  }

  @include rwd(bpSp) {
    margin-top: 84px;

    >a {
      padding: 66px 100px;

      >.title {
        top: -40px;
        left: 50%;

        >b {
          font-size: 6rem;
        }
      }

      >.txt {
        >.txt-lead-a {
          font-size: 2.4rem;

          +p {
            margin-top: 19px;
          }
        }

        >.arrow {
          bottom: 16px;
          right: 16px;
        }
      }

      >.img {
        left: -5px;
        top: 20px;
        width: 140px;
        height: 140px;
      }
    }
  }

  @include rwd(bpSp, below) {
    margin-top: 72.6px;

    >a {
      padding: 42px 20px 77px;

      >.title {
        top: -30px;
        left: 50%;

        >b {
          font-size: 4rem;
        }
      }

      >.txt {
        >.txt-lead-a {
          font-size: 2rem;

          +p {
            margin-top: 25px;
          }
        }

        >.arrow {
          bottom: 20px;
          right: 20px;
        }
      }

      >.img {
        left: -5px;
        top: 5px;
        width: 80px;
        height: 80px;
      }
    }
  }
}

.btn-business {
  display: none;
  z-index: 5;

  [data-script-enabled="true"] & {
    display: inline-block;
    position: absolute;
    bottom: 0;
    width: 62px;
    height: 31px;
    border-radius: 1000px 1000px 0 0;
    background: $bright-color-01;
    font-size: 0;
    color: transparent;

    &:focus {
      outline: 2px solid black;
    }

    @include rwd(bpSp) {
      right: 32px;
    }

    @include rwd(bpSp, below) {
      right: 50%;
      transform: translate(50%, 0);
    }

    &::before,
    &::after {
      @include pseudo();
      width: 15px;
      height: 3px;
      background: $gradient-color-01;
      border-radius: 1.5px;
      transition: .3s;
    }

    &::before {
      transform: rotate(45deg);
      bottom: 6px;
      left: 33%;
    }

    &::after {
      transform: rotate(135deg);
      bottom: 6px;
      left: 48%;
    }
  }
}

.mv-btn {
  [data-script-enabled="true"] & {
    position: absolute;
    right: 20px;
    bottom: 20px;
    width: 10px;
    height: 10px;
    font-size: 0;
    color: transparent;
    z-index: 2;

    &.is-pause {
      &::before {
        @include pseudo();
        top: 0;
        left: 0;
        width: 0;
        height: 0;
        border-top: 5px solid transparent;
        border-right: 5px solid transparent;
        border-bottom: 5px solid transparent;
        border-left: 10px solid $bright-color-01;
      }
    }

    &:not(.is-pause) {
      &::before {
        @include pseudo();
        top: 0;
        left: 0;
        width: 3px;
        height: 100%;
        background: $bright-color-01;
      }

      &::after {
        @include pseudo();
        top: 0;
        right: 0;
        width: 3px;
        height: 100%;
        background: $bright-color-01;
      }
    }
  }
}

.btn-entry {
  margin-top: 32px;

  >.btn-wrap {
    display: block;
    text-align: center;

    >.btn {
      position: relative;
      display: inline-flex;
      justify-content: center;
      min-width: 200px;
      padding: 15px;
      color: $bright-color-01;
      font-size: 1.4rem;
      font-weight: $font-semi-bold;
      text-decoration: none;
      text-align: center;
      background: $gradient-color-01;
      border-radius: 25px;
      transition: opacity .3s;

      @include hover() {
        opacity: .8;
      }

      >.icon {
        position: static;
        top: auto;
        left: 0;
        display: inline-block;
        width: auto;
        margin-right: 8px;
      }
    }
  }
}
