@use '../../_common/'as *;

// h1固定
.hdg-wrapper {
  position: relative;
  width: 100%;

  @include rwd(bpSp, above) {
    padding-top: 40px;
  }

  @include rwd(bpSp, below) {
    padding-bottom: 63px;
  }
}

.hdg-area {
  background: $bright-color-10;
  border-radius: 0 20px 20px 0;

  .txt {
    .hdg-a {
      font-weight: $font-medium;
      color: $strong-color-01;

      +p {
        font-size: 1.4rem;
        margin-top: 8px;
      }
    }
  }

  .img {
    position: absolute;
    right: 0;

    img {
      border-radius: 20px 0 0 20px;
      object-fit: cover;
    }
  }

  @include rwd(bpSp, above) {
    max-width: calc(100% - 190px);
    min-height: 242px;
    padding: 67px 260px 67px 70px;

    &::before {
      @include pseudo();
      top: -160px;
      left: -210px;
      width: 0;
      height: 0;
      border: 200px solid transparent;
      border-top: 200px solid transparent;
      border-right: 200px solid $strong-color-02;
      border-radius: 50%;
      transform: rotate(45deg);
      opacity: .05;
    }

    .txt {
      display: flex;
      flex-direction: column;
      justify-content: center;
      min-height: 108px;
      height: 100%;

      .hdg-a {
        font-size: 4rem;
      }
    }

    .img {
      top: 0;

      img {
        width: 427px;
      }
    }
  }

  @include rwd(1024px, above) {
    padding: 67px 460px 67px 70px;

    .img {
      img {
        width: 570px;
      }
    }
  }

  @include rwd(bpSp, below) {
    max-width: calc(100% - 20px);
    min-height: 156px;
    padding: 20px 20px 86px;

    &::before {
      @include pseudo();
      top: -108px;
      left: -110px;
      width: 0;
      height: 0;
      border: 110px solid transparent;
      border-top: 110px solid transparent;
      border-right: 110px solid $strong-color-02;
      border-radius: 50%;
      transform: rotate(45deg);
      opacity: .05;
    }

    .txt {
      .hdg-a {
        font-size: 2.4rem;

        +p {
          font-size: 1.4rem;
        }
      }
    }

    .img {
      bottom: 0;

      img {
        width: 276px;
      }
    }
  }
}

.hdg-b {
  position: relative;
  border-bottom: solid 1px $bright-color-11;
  padding-bottom: 8px;
  font-weight: $font-bold;

  &::before {
    @include pseudo();
    bottom: -1px;
    left: 0;
    width: 32px;
    height: 2px;
    background: $strong-color-01;
  }

  @include rwd(bpSp, above) {
    margin-top: 80px;
  font-size: 3.2rem;

  +* {
    margin-top: 40px;
  }

  + p {
    margin-top: 40px !important;
  }
}

  @include rwd(bpSp, below) {
    margin-top: 48px;
    font-size: 2.2rem;

    +* {
      margin-top: 24px;
    }

    + p {
      margin-top: 24px !important;
    }
  }
}

.hdg-c {
  font-weight: $font-bold;
  color: $strong-color-01;
  margin-top: 40px;

  +[class^="hdg-"] {
    margin-top: 32px;
  }

  +p {
    margin-top: 16px !important;
  }

  +.lyt-col-2 {
    margin-top: 32px;
  }

  .list-challenge-a & {
      text-align: center;
  }

  @include rwd(bpSp, above) {
    font-size: 2.4rem;
  }

  @include rwd(bpSp, below) {
    font-size: 2rem;
  }

  @include rwd(bpSp, below) {
    font-size: 2rem;
  }
}

.hdg-d {
  position: relative;
  padding-left: 16px;
  margin-top: 48px;
  font-weight: $font-bold;

  &::before {
    @include pseudo();
    top: 8px;
    left: 0;
    width: 4px;
    background: $strong-color-01;
    border-radius: 2px;
  }

  +* {
    margin-top: 16px;
  }

  + p {
    margin-top: 16px !important;
  }

  @include rwd(bpSp, above) {
    font-size: 1.6rem;

    &::before {
      top: 3px;
      height: 20px;
    }
  }

  @include rwd(bpSp, below) {
    font-size: 1.6rem;

    &::before {
      top: 8px;
      height: 16px;
    }
  }
}

.hdg-e {
  text-align: center;
  font-family: $font-family-02;
  font-weight: $font-semi-bold;
  color: $strong-color-01;

  >.hdg-detail {
    display: block;
    font-family: $font-family-01;
    font-weight: $font-bold;
    color: ;
  }

  @include rwd(bpSp, above) {
    font-size: 5.9rem;

    +p {
      margin-top: 27px;
    }

    >.hdg-detail {
      font-size: 2rem;
    }
  }

  @include rwd(bpSp, below) {
    font-size: 3.2rem;

    +p {
      margin-top: 25px;
    }

    >.hdg-detail {
      font-size: 1.6rem;
    }
  }
}

.hdg-history {
  position: relative;
  font-family: $font-family-02;
  font-weight: $font-semi-bold;
  color: $strong-color-01;
  padding-bottom: 25px;

  &::before {
    @include pseudo();
    left: -1px;
    background: $strong-color-01;
    border-radius: 1000px;
    transform: translate(-50%, -50%);
  }

  @include rwd(bpSp, above) {
    font-size: 5.6rem;
    padding-left: 47px;

    &::before {
      top: 45px;
      width: 32px;
      height: 32px;
    }
  }

  @include rwd(bpSp, below) {
    font-size: 3.2rem;
    padding-left: 16px;

    &::before {
      top: 25px;
      width: 16px;
      height: 16px;
    }
  }
}

.hdg-f-inner {
  @include rwd(bpSp, below) {
    text-align: center;
  }
}

.hdg-f {
  position: relative;
  display: inline-block;
  font-weight: $font-medium;

  &::before {
    @include pseudo();
    left: 0;
    bottom: 0;
    width: 100%;
    height: 4px;
    background-image: url('/docs/company/recruit/graduates/common/images/hdg-underline-01.svg');
    background-size: 12px 4px;
    background-position: left bottom;
    background-repeat: repeat-x;
  }

  @include rwd(bpSp, above) {
    font-size: 2rem;
    padding-bottom: 11px;
    margin-bottom: 32px;
  }

  @include rwd(bpSp, below) {
    font-size: 1.6rem;
    padding-bottom: 9px;
    margin-bottom: 24px;
  }
}

.hdg-top-inner {
  text-align: center;

  .box-about &,
  .box-person & {
    text-align: left;
    z-index: 1;
  }

  .box-challenge & {
    position: absolute;
    z-index: 1;

    @include rwd(bpSp, above) {
      top: 80px;
      right: -20px;
    }

    @include rwd(bpSp, below) {
      top: 50px;
      right: -10px;
    }
  }

  .box-career & {
    position: absolute;
    right: 0;

    @include rwd(bpSp, above) {
      top: -97px;
    }

    @include rwd(bpSp, below) {
      top: -40px;
    }
  }
}

.hdg-top {
  display: inline-block;
  background: $gradient-color-01;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: $font-family-02;
  font-weight: $font-semi-bold;
  line-height: 1.1;

  @include rwd(bpSp, above) {
    font-size: 8rem;
  }

  @include rwd(bpSp, below) {
    font-size: 5rem;
  }

  .box-about &,
  .box-challenge &,
  .box-person &,
  .box-career & {
    position: relative;

    @include rwd(bpSp, above) {
      font-size: 14rem;
    }

    @include rwd(bpSp, below) {
      font-size: 6.5rem;
    }
  }

  .box-about & {
    margin-left: -10px;
    margin-top: -74px;
  }

  .box-challenge & {
    @include rwd(bpSp, above) {
      margin-top: -60px;
      font-size: 12rem;
    }

    @include rwd(bpTab) {
      margin-top: 20px;
    }
  }
}

.hdg-recommend {
  text-align: center;
  font-weight: $font-bold;

  >.detail {
    display: block;
  }

  @include rwd(bpSp) {
    margin-bottom: 33px;
    font-size: 3.2rem;

    >.detail {
      font-size: 1.6rem;
    }
  }

  @include rwd(bpSp, below) {
    margin-bottom: 32px;
    font-size: 2.8rem;

    >.detail {
      font-size: 1.4rem;
    }
  }
}

.hdg-g-wrapper {
  position: relative;

  >.hdg-g {
    position: absolute;
    left: 50%;
    top: 50%;
    width: calc(100% - 20px);
    transform: translate(-50%, -50%);
    text-align: center;
  }

  @include rwd(bpSp) {
    margin-top: 120px;
    font-size: 2.8rem;
    line-height: .9;

    +* {
      margin-top: 56px !important;
    }

    >.img {
      img {
        border-radius: 20px;
      }
    }
  }

  @include rwd(bpSp, below) {
    margin-top: 56px;
    margin-left: -20px;
    font-size: 1.8rem;

    +* {
      margin-top: 32px !important;
    }

    >.img {
      img {
        border-radius: 0 20px 20px 0;
      }
    }
  }
}

.hdg-career-a {
  display: flex;
  position: relative;
  border-bottom: solid 1px $bright-color-11;
  padding-bottom: 8px;

  &::before {
    @include pseudo();
    bottom: -1px;
    left: 0;
    width: 32px;
    height: 2px;
    background: $strong-color-01;
  }

  >.mark {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    border-radius: 1000px;
    color: $bright-color-01;
    background: $strong-color-01;
    font-family: $font-family-02;
  }

  >.txt {
    font-weight: $font-bold;
  }

  @include rwd(bpSp) {
    margin-top: 82px;

    >.mark {
      width: 32px;
      height: 32px;
      margin-right: 12px;
      font-size: 2rem;
      transform: translateY(10px);
    }

    >.txt {
      font-size: 3.2rem;
    }

    +p {
      margin-top: 24px;
    }
  }

  @include rwd(bpSp, below) {
    margin-top: 50px;

    >.mark {
      width: 28px;
      height: 28px;
      margin-right: 8px;
      font-size: 1.6rem;
    }

    >.txt {
      font-size: 2.2rem;
    }

    +p {
      margin-top: 16px;
    }
  }
}
