@use '../../_common/'as *;

.js-tab {
  [data-script-enabled="true"] & {
    >ul {
      >li {
        &.is-active {
          >a {
            position: relative;
            color: $tab-color-01;
            background: $bright-color-03;

            &::before {
              @include pseudo();
              bottom: 0;
              left: 50%;
              width: calc(100% - 40px);
              height: 1px;
              background: $tab-color-01;
              transform: translate(-50%, 0);
            }
          }
        }
      }
    }
  }

  &.tab-business {
    position: relative;

    >ul {
      position: absolute;
      display: flex;
      z-index: 2;

      >li {
        margin-left: 4px;

        >a {
          display: block;
          width: 100%;
          text-align: center;
          text-decoration: none;
          color: $bright-color-01;
          background: $tab-color-01;
          border-radius: 10px 10px 0 0;
        }
      }
    }

    @include rwd(bpSp) {
      [data-script-enabled="true"] & {
        >ul {
          >li {
            &.is-active {
              >a {
                padding: 12px;

                &::before {
                  width: calc(100% - 40px);
                }
              }
            }
          }
        }
      }

      >ul {
        top: -48px;
        left: 128px;

        >li {
          &:first-of-type {
            margin-left: 0;
          }

          >a {
            max-width: 240px;
            min-width: 240px;
            padding: 12px 12px 10px 12px;
          }
        }
      }
    }

    @include rwd(bpSp, below) {
      [data-script-enabled="true"] & {
        >ul {
          >li {
            &.is-active {
              >a {
                padding: 6px 12px;

                &::before {
                  width: calc(100% - 20px);
                }
              }
            }
          }
        }
      }

      >ul {
        top: -33px;
        left: 20px;

        >li {
          >a {
            max-width: 181px;
            min-width: 110px;
            font-size: 1.4rem;
            padding: 6px 12px 4px 12px;
          }
        }
      }
    }

    @include rwd(375px, below) {
      >ul {
        top: calc(-27px + (-33 - -27) * (100vw - 320px) / (375 - 320));

        >li {
          >a {
            font-size: calc(10px + (14 - 10) * (100vw - 320px) / (375 - 320));
          }
        }
      }
    }
  }
}

.js-tab-item {
  display: block;

  [data-script-enabled="true"] & {
    display: none;

    &.is-active {
      display: block;
    }
  }
}
