@use '../../_common/'as *;

.lyt-history {
  >* {
    &:first-of-type {
      margin: 0;
    }
  }

  >.lyt-img-history {
    flex-shrink: 0;
    max-width: 200px;

    >img {
      width: 100%;
      border-radius: 20px;
      border: solid 1px $bright-color-11;
    }
  }

  @include rwd(bpSp, above) {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;

    >.lyt-img-history {
      margin-left: 40px;
    }
  }

  @include rwd(bpSp, below) {
    >.lyt-img-history {
      margin: 32px auto 0;
    }
  }
}

.lyt-img-top {
  position: relative;
  border-bottom: solid 2px $bright-color-02;

  &::before {
    @include pseudo();
    background: url('/docs/company/recruit/graduates/common/images/decoration-top-01.svg') center / cover;
    z-index: 3;
    animation: pickUpAnimation 15s linear infinite reverse;
  }

  >.img-content {
    .swiper-slide {
      position: relative;

      >.img-item {
        position: relative;

        &::after {
          @include pseudo();
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: $gradient-color-03;
          border-radius: 20px;
        }

        >img {
          border-radius: 20px;
        }
      }

      >.txt-item {
        position: absolute;
        bottom: 0;
        left: 0;
        padding: 0 32px 32px;
        color: $bright-color-01;
        z-index: 1;

        >.title {
          +p {
            font-size: 1.4rem;
          }
        }
      }
    }
  }

  >.txt-content {
    max-width: 680px;
    width: 100%;

    >.hdg-c {
      margin-top: 0;
      font-weight: $font-regular;
    }
  }

  @include rwd(bpTab) {
    display: flex;
    margin-top: 83px;
    padding-bottom: 56px;

    &::before {
      width: 130px;
      height: 130px;
      top: -60px;
      left: -60px;
    }

    >.img-content {
      .swiper-slide {
        >.txt-item {
          >.title {
            font-size: 1.8rem;
          }
        }
      }
    }

    >.txt-content {
      margin-left: 48px;

      >.hdg-c {
        font-size: 2rem;
      }
    }
  }

  @include rwd(bpTab, below) {
    margin-top: 93px;
    padding-bottom: 32px;

    &::before {
      width: 92px;
      height: 92px;
      top: -40px;
      left: -40px;
    }

    >.txt-content {
      margin-top: 34px;

      >.hdg-c {
        +p {
          margin-top: 11px;
        }
      }
    }
  }

  @include rwd(bpSp, below) {
    >.img-content {
      .swiper-slide {
        >.txt-item {
          padding: 0 24px 25px;

          >.title {
            font-size: 1.6rem;
          }
        }
      }
    }

    >.txt-content {
      >.hdg-c {
        font-size: 1.6rem;
      }
    }
  }
}

.lyt-list-top {
  @include rwd(bpSp, above) {
    margin-top: 47px;
  }

  @include rwd(bpSp, below) {
    margin-top: 41px;

    >ul {
      >li {
        &:first-of-type {
          >.lyt-top-a {
            >a {
              padding-top: 0;
            }
          }
        }
      }
    }
  }
}

.lyt-top-a {
  position: relative;

  >a {
    position: relative;
    display: block;
    text-decoration: none;
    color: ;

    &::before,
    &::after {
      @include pseudo();
      height: 3px;
      background: $gradient-color-01;
      border-radius: 1.5px;
      transition: .3s;
    }

    >.img {
      >img {
        border-radius: 20px;
      }
    }

    >.txt {
      position: relative;

      &::before {
        @include pseudo();
        left: 0;
        width: 32px;
        height: 29px;
        background: url('/docs/company/recruit/graduates/common/images/decoration-top-02.svg');
      }

      >.txt-lead-a {
        font-weight: $font-medium;
      }

      >.detail {
        position: relative;
      }
    }
  }

  &::before {
    @include pseudo();
    height: 2px;
    background: $bright-color-02;
  }

  @include rwd(bpSp, above) {
    padding-bottom: 48px;

    >a {
      display: flex;

      @include hover() {
        &::before,
        &::after {
          right: -1%;
        }
      }

      &::before,
      &::after {
        width: 24px;
        right: 0;
      }

      &::before {
        transform: rotate(40deg);
        top: 48%;
      }

      &::after {
        transform: rotate(-40deg);
        top: 55%;
      }

      >.img {
        max-width: 276px;
      }

      >.txt {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        align-items: center;
        padding-left: 42px;
        padding-right: 42px;
        margin-left: 44px;

        &::before {
          top: 0;
        }

        >.txt-lead-a {
          max-width: 440px;
          width: 100%;
          font-size: 2.8rem;
        }

        >.detail {
          margin-left: 32px;

          >.detail-join {
            font-size: 1.4rem;
          }

          >.detail-name {
            font-size: 1.8rem;
          }
        }
      }
    }

    &::before {
      bottom: 24px;
      right: 0;
      width: calc(100% - 310px);
    }
  }

  @include rwd(bpSp, below) {
    >a {
      padding: 43px 0;

      &::before,
      &::after {
        width: 20px;
        right: 0;
      }

      &::before {
        transform: rotate(40deg);
        bottom: 55px;
      }

      &::after {
        transform: rotate(-40deg);
        bottom: 43px;
      }

      >.img {
        padding: 0 64px;
      }

      >.txt {
        padding: 32px 20px 0 42px;

        &::before {
          top: 8px;
        }

        >.txt-lead-a {
          font-size: 2rem;
        }

        >.detail {
          margin-top: 14px;

          >.detail-join {
            font-size: 1.2rem;
          }

          >.detail-name {
            margin-top: 8px;
            font-size: 1.6rem;
          }
        }
      }
    }

    &::before {
      bottom: 0;
      right: 0;
      width: 100%;
    }
  }
}

.lyt-img-a {
  display: table;
  table-layout: fixed;
  text-align: center;
  margin-left: auto;
  margin-right: auto;

  .modal-wrap._type-a & {
    margin-top: 0;

    >.img-wrap {
      img {
        max-width: none;
      }
    }
  }

  >.img-wrap {
    &._has-frame {
      position: relative;
      display: table-cell;

      &::before {
        @include pseudo();
        border-radius: 5px;
        border: 1px solid $bright-color-13;
        width: 100%;
        height: 100%;
      }
    }

    &._card {
      >img {
        max-width: 240px;
      }
    }

    img {
      border-radius: 5px;
      max-width: 980px;
      width: 100%;
    }
  }

  ._crosstalk & {
    >.img-wrap {
      border-radius: 30px;
    }

    + .box-crosstalk-b {
      margin-top: 56px;
    }
  }

  @include rwd(bpSp, above) {
    margin-top: 40px;
  }

  @include rwd(bpSp, below) {
    margin-top: 32px;
  }
}

.lyt-img-b {
  margin-top: 32px;

  >.img-wrap {
    &._has-frame {
      position: relative;

      &::before {
        @include pseudo();
        border-radius: 20px;
        border: 1px solid #e6e6e6;
        width: 100%;
        height: 100%;
      }
    }

    >img {
      border-radius: 20px;
    }
  }

  >.txt {
    >* {
      &:first-child {
        margin-top: 0;
      }
    }
  }

  .col & {
    align-items: flex-start;

    >.img-wrap {
      max-width: 150px;
      min-width: auto;
      flex-shrink: 0;
    }
  }

  .box-a & {
    >.img-wrap {
      max-width: 240px;
      min-width: auto;
      flex-shrink: 0;
    }
  }

  @include rwd(bpSp, above) {
    display: flex;
    justify-content: space-between;

    &._reverse,
    &._col-reverse {
      flex-direction: row-reverse;

      >.txt {
        margin-left: 40px;
        margin-right: 0;
      }
    }

    .col & {
      &._reverse,
      &._col-reverse {
        +.lyt-img-b {

          &._reverse,
          &._col-reverse {
            margin-top: 20px;
          }
        }

        >.txt {
          margin-left: 24px;
          margin-right: 0;

          >.title {
            font-size: 1.6rem;
          }
        }
      }
    }

    >.txt {
      margin-right: 40px;
      margin-left: 0;
    }

    >.img-wrap {
      max-width: 500px;
      min-width: 350px;
      width: 100%;
    }
  }

  @include rwd(900px, below) {
    .box-a & {
      display: block;

      >.img-wrap {
        margin-left: auto;
        margin-right: auto;
        margin-top: 32px;
      }
    }
  }

  @include rwd(bpSp, below) {
    .col & {
      >.txt {
        >.title {
          font-size: 1.4rem;
        }
      }
    }

    &._col-reverse {
      display: flex;
      flex-direction: column-reverse;

      >.img-wrap {
        margin-top: 0;
        margin-bottom: 32px;
        margin-left: auto;
        margin-right: auto;
      }
    }

    >.img-wrap {
      margin-left: auto;
      margin-right: auto;
      margin-top: 32px;
    }
  }
}

.lyt-card-a {
  margin-top: 24px;

  >ul {
    >li {
      border-radius: 4px;
      background: $bright-color-01;

      >div {
        >.txt {
          >.title {
            font-size: 1.6rem;
            font-weight: $font-medium;
            border-bottom: dashed 2px $bright-color-11;
  
            +p {
              margin-top: 12px;
            }
          }
        }
      }
    }
  }

  @include rwd(bpSp, above) {
    >ul {
      display: flex;
      flex-wrap: wrap;
      margin: 0 0 -8px -8px;

      >li {
        width: calc((100% / 2) - 8px);
        margin: 0 0 8px 8px;

        >div {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          height: 100%;
          padding: 26px 24px;

          >.txt {
            >.title {
              padding-bottom: 16px;
            }
          }
        }
      }
    }
  }

  @include rwd(bpSp, below) {
    >ul {
      >li {
        margin-top: 8px;

        &:first-of-type {
          margin-top: 0;
        }

        >div {
          padding: 22px 20px;

          >.txt {
            >.title {
              padding-bottom: 12px;
            }
          }
        }
      }
    }
  }
}

.lyt-col-a {
  margin-top: 32px;

  @include rwd(bpSp, below) {
    >ul {
      >li {
        padding: 12px 24px;
      }
    }
  }

  @include rwd(bpSp, above) {
    >ul {
      display: flex;

      >li {
        padding: 0 70px;
      }
    }
  }
}

.lyt-card-number {
  >ul {
    >li {
      >a {
        &.lyt-card-item {
          @include hover {
            background: $bright-color-01;
          }
        }
      }

      >.lyt-card-item {
        display: block;
        border-radius: 20px;
        text-decoration: none;
        color: ;

        &._color-a {
          background: $lyt-num-color-01;
        }

        &._color-b {
          background: $lyt-num-color-02;
        }

        &._color-c {
          background: $lyt-num-color-03;
        }

        >.txt {
          >.title {
            font-weight: $font-bold;
          }

          >.num {
            color: $strong-color-01;
            font-weight: $font-bold;
          }
        }
      }
    }
  }

  [data-script-enabled="true"] & {
    >ul {
      >li {
        >.lyt-card-item {
          opacity: 0;
          transition: .3s;

          &.is-animation {
            animation: cardAnimation .5s ease-in-out forwards;
          }
        }
      }
    }
  }

  @include rwd(767px) {
    >ul {
      display: flex;
      flex-wrap: wrap;
      margin: 0 0 -24px -24px;

      >li {
        width: calc((100% / 2) - 24px);
        margin: 0 0 24px 24px;

        >.lyt-card-item {
          padding: 45px;
          height: 100%;

          &._type-a {
            >.img {
              >img {
                max-width: 250px;
              }
            }
          }

          >.txt {
            text-align: center;

            >.title {
              font-size: 1.8rem;
              font-weight: $font-bold;
              margin-top: 0;
              padding-bottom: 16px;
              border-bottom: solid 1px $bright-color-09;
            }

            >.num {
              font-size: 4rem;
              font-weight: $font-bold;

              >.param {
                font-size: 2rem;
              }
            }
          }

          >.img {
            margin-top: 20px;

            >img {
              max-width: 120px;
              margin: 0 auto;
            }
          }
        }
      }
    }
  }

  @include rwd(bpTab) {
    >ul {
      >li {
        width: calc((100% / 3) - 24px);
      }
    }
  }

  @include rwd(767px, below) {
    >ul {
      >li {
        margin-top: 16px;

        >.lyt-card-item {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 28px 30px 28px;

          &._color-b {
            >.img {
              >img {
                max-width: 60px;
                margin: 0 auto;
              }
            }
          }

          &._type-a {
            display: block;

            >.txt {
              text-align: center;

              >.title {
                padding-bottom: 13px;
                border-bottom: solid 1px $bright-color-09;
              }
            }

            >.img {
              margin-top: 21px;
              margin-left: 0;

              >img {
                max-width: 190px;
                margin: 0 auto;
              }
            }
          }

          &._type-b {
            >.txt {
              >.num {
                display: block;
                line-height: 1;
                margin-top: 10px;

                >.param {
                  &:first-of-type {
                    display: block;
                  }
                }
              }
            }
          }

          >.img {
            margin-left: 30px;

            >img {
              max-width: 60px;
              margin: 0 auto;
            }
          }

          >.txt {
            >.title {
              font-size: 1.6rem;
            }

            >.num {
              font-size: 3.2rem;

              >.param {
                font-size: 2rem;
              }
            }
          }
        }
      }
    }
  }
}

.lyt-img-modal-a {
  &._color-a {
    >.img-content {
      background: $lyt-num-color-01;
    }
  }

  &._color-b {
    >.img-content {
      background: $lyt-num-color-02;
    }
  }

  &._color-c {
    >.img-content {
      background: $lyt-num-color-03;
    }
  }

  >.img-content {
    flex-shrink: 0;

    >.txt {
      >.title {
        font-weight: $font-bold;
        margin-top: 0;
      }

      >.num {
        font-weight: $font-bold;
        color: $strong-color-01;
      }
    }
  }

  >.txt-content {
    position: relative;

    >.txt {
      .modal-link {
        >a {
          text-decoration: none;
          color: ;
        }
      }
    }
  }

  @include rwd(897px, above) {
    display: flex;

    &._type-a {
      >.img-content {
        >.img {
          >img {
            max-width: 320px;
          }
        }
      }
    }

    >.img-content {
      max-width: 400px;
      width: 100%;
      padding: 40px;
      border-radius: 20px 0 0 20px;

      >.img {
        margin-top: 37px;

        >img {
          max-width: 200px;
          margin: 0 auto;
        }
      }

      >.txt {
        text-align: center;

        >.title {
          font-size: 1.8rem;
          padding-bottom: 16px;
          border-bottom: solid 1px $bright-color-09;
        }

        >.num {
          font-size: 5rem;

          >.param {
            font-size: 2.4rem;
          }
        }
      }
    }

    >.txt-content {
      width: 100%;
      padding: 70px;
      border-radius: 0 20px 20px 0;

      >.txt {
        .modal-link {
          position: absolute;
          right: 32px;
          bottom: 32px;
        }
      }
    }
  }

  @include rwd(897px, below) {
    &._type-a {
      >.img-content {
        display: block;

        >.img {
          max-width: 210px;
          margin: 0 auto;
        }
      }
    }

    >.img-content {
      display: flex;
      justify-content: center;
      border-radius: 20px 20px 0 0;
      padding: 30px 20px 20px;

      >.txt {
        >.title {
          font-size: 1.6rem;
        }

        >.num {
          font-size: 3.2rem;

          >.param {
            font-size: 2rem;
          }
        }
      }

      >.img {
        max-width: 290px;
        margin-left: 30px;
      }
    }

    >.txt-content {
      padding: 20px 20px 30px;

      >.txt {
        >.title {
          font-size: 1.6rem;
        }

        >.num {
          font-size: 3.2rem;

          >.param {
            font-size: 2rem;
          }
        }

        .modal-link {
          margin-top: 16px;
          text-align: right;
        }
      }
    }
  }
}

.lyt-col-2 {
  >.col {
    >* {
      &:first-child {
        margin-top: 0;
      }
    }
  }

  &._type-a {
    >.col {
      position: relative;

      >* {
        &:first-of-type {
          margin-top: 0;
        }
      }

      &::before {
        background: $bright-color-11;
      }
    }
  }

  @include rwd(bpSp, above) {
    display: flex;
    flex-wrap: wrap;
    margin-top: 56px;
    margin-left: -32px;

    &._type-a {
      margin-left: -48px;
      margin-bottom: -22px;

      >.col {
        width: calc((100% / 2) - 48px);
        margin-left: 48px;
        margin-bottom: 22px;

        &:nth-of-type(even) {
          &::before {
            @include pseudo;
            top: 0;
            left: -24px;
            width: 1px;
            height: 100%;
          }
        }
      }
    }

    >.col {
      width: calc((100% / 2) - 32px);
      margin-left: 32px;
    }
  }

  @include rwd(bpSp, below) {
    margin-top: 24px;

    &._type-a {
      >.col {
        padding: 24px 0;

        &::before {
          @include pseudo;
          top: 0;
          left: 0;
          width: 100%;
          height: 1px;
        }
      }
    }

    >.col {
      + .col {
        margin-top: 24px;
      }
    }
  }
}

.lyt-img-c {
  margin-top: 56px;

  >ul {
    display: flex;
    justify-content: center;

    >li {
      width: 100%;

      >.img-wrap {
        &._has-frame {
          padding: relative;

          &::before {
          @include pseudo();
          border-radius: .5rem;
          border: 1px solid #e6e6e6;
          width: 100%;
          height: 100%;
          }
        }
      }
    }
  }

  @include rwd(bpSp, above) {
    >ul {
      >li {
        max-width: 160px;
        margin-left: 24px;
      }
    }
  }

  @include rwd(bpSp, below) {
    >ul {
      >li {
        max-width: 100px;
        margin-left: 16px;
      }
    }
  }
}

.lyt-img-d {
  background: $bright-color-03;
  border-radius: 20px;

  >.txt {
    >.title {
      font-size: 2rem;
      font-weight: $font-bold;
      padding-bottom: 11px;
      border-bottom: solid 1px $bright-color-11;
      margin-top: 0;
    }
  }

  >.img-wrap {
    max-width: 128px;
    max-height: 128px;

    &._has-frame {
      position: relative;

      &::before {
        @include pseudo();
        border-radius: 20px;
        border: 1px solid #e6e6e6;
        width: 100%;
        height: 100%;
      }
    }

    >img {
      border-radius: 20px;
    }
  }

  @include rwd(bpSp, above) {
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
    padding: 20px;
    margin-top: 48px;

    >.txt {
      width: 100%;
      margin-left: 23px;

      >.title {
        +p {
          margin-top: 18px;
        }
      }
    }

    >.img-wrap {
      flex-shrink: 0;
    }
  }

  @include rwd(bpSp, below) {
    padding: 26px 20px 40px;
    margin-top: 29px;

    >.img-wrap {
      &._has-frame {
        margin-top: 24px;
        margin-left: auto;
        margin-right: auto;
      }
    }
  }
}

.lyt-img-e {
  display: flex;

  >.txt {
    >* {
      &:first-of-type {
        margin-top: 0;
      }
    }
  }

  >.img {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    border-radius: 1000px;
    background: $bright-color-03;
  }

  @include rwd(bpSp, above) {
    >.txt {
      margin-left: 24px;
    }

    >.img {
      width: 110px;
      height: 110px;
    }
  }

  @include rwd(bpSp, below) {
    >.txt {
      margin-left: 16px;
    }

    >.img {
      width: 64px;
      height: 64px;
    }
  }
}
