@use '../../_common/' as *;

.txt-lead-a {
  @include rwd(bpSp) {
    text-align: center;
  }

  > b {
    font-weight: normal;
    line-height: 1.6;

    @include rwd(bpSp, below) {
      font-size: 1.8rem;
    }

    @include rwd(bpSp) {
      font-size: 2rem;
    }
  }

  .box-about-inner & {
    @include rwd(bpSp) {
      text-align: center;
      font-size: 2.8rem;

      + p {
        margin-top: 36px;
        text-align: center;
      }
    }

    @include rwd(bpSp, below) {
      font-size: 2rem;

      + p {
        margin-top: 18px;
      }
    }
  }

  .box-challenge-inner & {
    text-align: left;

    @include rwd(bpSp) {
      font-size: 2.8rem;

      + p {
        margin-top: 36px;
      }
    }

    @include rwd(bpSp, below) {
      font-size: 2rem;

      + p {
        margin-top: 22px;
      }
    }
  }

  .box-person-inner & {
    text-align: left;
  }

  .box-message-inner & {
    text-align: left;

    @include rwd(bpSp) {
      font-size: 2rem;
    }

    @include rwd(bpSp, below) {
      font-size: 1.6rem;
    }
  }
}
