@use '../_common/' as *;

footer {
  .footer-upper-contents {
    background: $gradient-color-01;
    color: $bright-color-01;

    > .txt-lead {
      text-align: center;
    }
  }

  .footer-lower-contents {
    display: flex;
    justify-content: space-between;
    align-items: center;

    >.footer-link-a {
      >a {
        display: flex;
        align-items: center;

        >img {
          width: 14px;
          height: 14px;
          margin-left: 6px;
        }
      }
    }

    >.footer-txt-copylight {
      font-size: 1.2rem;
      font-weight: $font-regular;
    }
  }

  @include rwd(bpTab) {
    .footer-upper-contents {
      padding: 80px 70px 100px;

      > .txt-lead {
        font-size: 2.4rem;
      }
    }

    .footer-main-contents {
      display: flex;
      justify-content: space-between;
      padding: 48px 40px 89px;

      .footer-logo-area {
        >a {
          >img {
            width: 128px;
          }
        }
      }
    }

    .footer-menu-area {
      >ul{
        display: flex;
        align-items: baseline;

        a {
          text-decoration: none;
          color: ;
        }

        >li {
          margin-right: 29px;

          &:last-of-type {
            margin-right: 0;
          }

          >dl {
            >dt,
            >dd {
              font-size: 1.3rem;
            }

            >dt {
              border-bottom: solid 1px $bright-color-09;
              margin-bottom: 16px;
            }

            >dd {
              margin-top: 8px;
              font-weight: $font-regular;

              >a {
                color: $dark-color-03;
                transition: .1s;

                @include hover() {
                  opacity: .8;
                }
              }
            }
          }

          >a {
            padding-right: 14px;
            font-size: 1.3rem;
            border-bottom: solid 1px $bright-color-09;

            @include hover() {
              >.txt {
                &::before,
                &::after {
                  right: -15px;
                }
              }
            }

            >.txt {
              position: relative;

              &::before,
              &::after {
                @include pseudo();
                width: 6px;
                height: 2px;
                right: -13px;
                border-radius: 1px;
                background: $strong-color-01;
                transition: .1s;
              }

              &::before {
                top: 50%;
                transform: rotate(40deg) translate(-50%, 0);
              }

              &::after {
                top: 50%;
                transform: rotate(-40deg) translate(-50%, 0);
              }
            }
          }
        }
      }
    }

    .footer-lower-contents {
      position: relative;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 36px 70px;

      &::before {
        @include pseudo();
        top: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background: $gradient-color-01;
      }

      >.footer-link-a {
        >a {
          text-decoration: none;
          color: ;

          >.txt {
            font-size: 1.4rem;
            font-weight: $font-regular;
          }
        }
      }

      >.footer-txt-copylight {
        font-size: 1.2rem;
      }
    }
  }

  @include rwd(1170px, below) {
    .footer-main-contents {
      flex-direction: column;

      .footer-menu-area {
        margin-top: 26px;
      }
    }
  }

  @include rwd(bpTab, below) {
    .footer-upper-contents {
      padding: 44px 20px 60px;

      > .txt-lead {
        font-size: 2rem;
      }
    }

    .footer-main-contents {
      display: none;
    }

    .footer-lower-contents {
      flex-direction: column;
      padding: 46px 30px;

      &::before {
        @include pseudo();
        top: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background: $gradient-color-01;
      }

      >.footer-link-a {
        >a {
          text-decoration: none;
          color: ;

          >.txt {
            font-size: 1.4rem;
          font-weight: $font-regular;
          }
        }
      }

      >.footer-txt-copylight {
        margin-top: 32px;
      }
    }
  }
}

.footer-btn-list-a {
  >ul{
    .btn-items {
      >a {
        display: block;
        color: $bright-color-01;
        border: solid 1px $bright-color-01;
        border-radius: 20px;
        text-decoration: none;
        transition: .2s;

        @include hover() {
          background: $bright-color-01;
          color: $strong-color-01;
        }

        >.txt {
          display: block;
          font-family: $font-family-02;
          font-weight: $font-semi-bold;
          text-align: center;

          >.txt-detail {
            display: block;
            font-weight: $font-regular;
            text-align: center;
          }
        }
      }
    }
  }

  @include rwd(bpTab) {
    margin-top: 45px;

    >ul{
      display: flex;
      justify-content: center;

      .btn-items {
        max-width: 403px;
        width: 100%;
        margin-right: 46px;

        >a {
          display: block;
          width: 100%;
          padding: 44px 20px;
          color: $bright-color-01;
          text-decoration: none;

          >.txt {
            display: block;
            font-family: $font-family-02;
            font-weight: $font-semi-bold;
            font-size: 3.2rem;
            text-align: center;

            >.txt-detail {
              display: block;
              font-weight: $font-regular;
              font-size: 1.6rem;
              text-align: center;
            }
          }
        }

        &:last-of-type {
          margin-right: 0;
        }
      }
    }
  }

  @include rwd(bpTab, below) {
    margin-top: 32px;

    >ul{
      display: flex;
      flex-direction: column;

      .btn-items {
        width: 100%;
        margin-top: 12px;

        >a {
          width: 100%;
          padding: 22px 20px;

          >.txt {
            font-size: 2.4rem;

            >.txt-detail {
              font-size: 1.4rem;
            }
          }
        }

        &:first-of-type {
          margin-top: 0;
        }
      }
    }
  }
}

.footer-list-a {
  margin-top: 8px;
  margin-bottom: 16px;

  >ul{
    >li {
      position: relative;
      margin-top: 8px;

      &::before {
        @include pseudo();
        top: 50%;
        left: 0;
        width: 6px;
        height: 2px;
        border-radius: 1px;
        background: $bright-color-08;
      }

      >a {
        padding-left: 13px;
        color: $dark-color-03;
        transition: .1s;

        @include hover() {
          opacity: .8;
        }
      }
    }
  }
}

.footer-list-b {
  @include rwd(bpTab) {
    margin: 16px 0 24px;

    >dl {
      >dt {
        font-size: 1.2rem;
        font-weight: $font-medium;
      }

      >dd {
        margin-top: 8px;

        a {
          color: $dark-color-03;
          font-size: 1.2rem;
          transition: .1s;

          @include hover() {
            opacity: .8;
          }
        }
      }
    }
  }
}

.footer-col2 {
  @include rwd(bpTab) {
    >ul{
      display: flex;
      flex-wrap: wrap;
      width: 272px;
      margin-top: -8px;
      margin-right: -16px;

      >li {
        width: 50%;
        padding-top: 8px;
        padding-right: 16px;
      }
    }
  }
}
