@use '../../_common/'as *;

.link-anchor-a {
  display: flex;

  >ul {
    display: flex;
    flex-wrap: wrap;

    >li {
      margin-left: 24px;
      margin-bottom: 24px;

      >a {
        position: relative;
        display: inline-block;
        width: 100%;
        padding: 11.5px;
        text-align: center;
        text-decoration: none;
        color: ;
        background: $bright-color-03;
        border-radius: 1000px;

        &::before,
        &::after {
          @include pseudo();
          width: 8px;
          height: 2px;
          bottom: -12px;
          border-radius: 20px;
          background: $gradient-color-01;
          transition: .3s;
        }

        &::before {
          left: 50.5%;
          transform: rotate(50deg) translate(-50%, 0);
        }

        &::after {
          left: 50%;
          transform: rotate(130deg) translate(-50%, 0);
        }

        @include hover() {
          &::before,
          &::after {
            bottom: -14px;
          }
        }
      }
    }
  }

  @include rwd(bpSp, above) {
    +* {
      margin-top: 80px !important;
    }

    >ul {
      margin: 0 auto;

      >li {
        min-width: 200px;
      }
    }
  }

  @include rwd(bpSp, below) {
    justify-content: center;

    +* {
      margin-top: 48px !important;
    }

    >ul {
      width: 100%;
      margin-left: -24px;
      margin-bottom: -24px;

      >li {
        max-width: calc((100% / 2) - 24px);
        width: 100%;
      }
    }
  }
}

.list-btn-a {
  margin-top: 24px;

  >ul {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin: -16px 0 0 -32px;

    >li {
      padding: 16px 0 0 32px;
      width: calc(100% / 3);
      max-width: 273px;

      >a {
        display: block;
        position: relative;
        text-decoration: none;
        color: ;
        padding: 19px 42px 19px 26px;

        &::before {
          @include pseudo();
          top: 50%;
          left: 35px;
          width: 70px;
          height: 70px;
          background: $bright-color-03;
          border-radius: 1000px;
          z-index: -1;
          transform: translate(-50%, -50%);
          transition: .6s;
        }

        @include hover() {
          &::before {
            width: 90px;
            height: 90px;
          }

          >.txt {
            >.arrow {
              &::before {
                transition: transform .4s cubic-bezier(.23, 1, .32, 1) 0s, opacity .3s cubic-bezier(.23, 1, .32, 1) 0s;
                opacity: 0;
                transform: translateX(300%);
              }

              &::after {
                transition: transform .4s cubic-bezier(.23, 1, .32, 1) .1s;
                transform: translateX(0);
              }
            }
          }
        }

        >.txt {
          >.arrow {
            position: absolute;
            top: 50%;
            right: 0;
            display: inline-block;
            width: 32px;
            height: 32px;
            border-radius: 1000px;
            background: $gradient-color-01;
            overflow: hidden;
            transform: translate(0, -50%);

            &::before,
            &::after {
              @include pseudo();
              top: 14px;
              right: 12px;
              width: 8px;
              height: 5px;
              background: url('/docs/company/recruit/graduates/common/images/icon-arrow-03.svg') center / cover;
            }

            &::after {
              transform: translateX(-300%);
            }
          }
        }
      }
    }
  }

  @include rwd(1194px, below) {
    >ul {
      >li {
        >a {
          >.txt {
            font-size: calc(14px + (16 - 14) * (100vw - 768px) / (1192 - 768));
          }
        }
      }
    }
  }

  @include rwd(bpSp, below) {
    margin-top: 32px;

    >ul {
      >li {
        >a {
          >.txt {
            font-size: 1.4rem;
          }
        }
      }
    }
  }

  @include rwd(425px, below) {
    >ul {
      >li {
        width: calc(100% / 2);
      }
    }
  }
}

.link-list-b {

  .box-recommend & {
    padding-top: 0;
  }

  >ul {
    >li {
      >a {
        position: relative;
        width: 100%;
        display: block;
        text-decoration: none;
        color: ;
        border-radius: 20px;
        overflow: hidden;

        @include hover() {
          >.txt-content {
            .arrow {
              &::before {
                transition: transform .4s cubic-bezier(.23, 1, .32, 1) 0s, opacity .3s cubic-bezier(.23, 1, .32, 1) 0s;
                opacity: 0;
                transform: translateX(300%);
              }

              &::after {
                transition: transform .4s cubic-bezier(.23, 1, .32, 1) .1s;
                transform: translateX(0);
              }
            }
          }

          >img {
            transform: scale(1.1);
          }
        }

        >.txt-content {
          position: absolute;
          top: 50%;
          left: 50%;
          width: 100%;
          padding: 9px 46px 9px 20px;
          background: $bright-color-01;
          border-radius: 1000px;
          transform: translate(-50%, -50%);

          p {
            margin-top: 0;
          }

          .arrow {
            position: absolute;
            top: 50%;
            right: 5px;
            display: inline-block;
            width: 32px;
            height: 32px;
            border-radius: 1000px;
            background: $gradient-color-01;
            overflow: hidden;
            transform: translate(0, -50%);

            &::before,
            &::after {
              @include pseudo();
              top: 14px;
              right: 12px;
              width: 8px;
              height: 5px;
              background: url('/docs/company/recruit/graduates/common/images/icon-arrow-03.svg') center / cover;
            }

            &::after {
              transform: translateX(-300%);
            }
          }
        }

        >img {
          filter: brightness(.7);
          object-fit: cover;
          transition: .3s;
        }
      }
    }
  }

  @include rwd(425px) {
    &._center {
      &._col-2 {
        >ul {
          justify-content: center;
          flex-wrap: nowrap;

          >li {
            width: 403px;
          }
        }
      }
    }

    >ul {
      display: flex;
      flex-wrap: wrap;
      margin: -25px 0 0 -20px;

      >li {
        width: calc(100% / 2);
        padding: 25px 0 0 20px;

        >a {
          max-width: 403px;

          >.txt-content {
            width: calc(310vw * 100 / 768);
            min-width: 160px;
            max-width: 310px;
            font-size: 1.4rem;
          }
        }
      }
    }
  }

  @include rwd(bpSp) {
    padding-top: 56px;

    >ul {
      display: flex;
      flex-wrap: wrap;
      margin: -48px 0 0 -46px;

      >li {
        width: calc(100% / 3);
        padding: 48px 0 0 46px;

        >a {
          width: 100%;

          >.txt-content {
            width: calc(260vw * 100 / 1140);
            min-width: 160px;
            max-width: 260px;
            font-size: 1.6rem;
          }
        }
      }
    }
  }

  @include rwd(bpSp, below) {
    padding-top: 32px;

    >ul {
      >li {
        >a {
          >.txt-content {
            font-size: 1.4rem;
          }
        }
      }
    }
  }

  @include rwd(425px, below) {
    >ul {
      >li {
        padding-bottom: 20px;

        &:last-of-type {
          padding-bottom: 0;
        }

        >a {
          >.txt-content {
            width: 100%;
            max-width: calc(100% - 75px);
          }
        }
      }
    }
  }
}

.link-a {
  margin-top: 16px;

  & + & {
    margin-top: 0;
  }

  >a {
    position: relative;
    display: inline-block;
    color: ;
    text-decoration: none;
  
    @include hover() {
      &::before,
      &::after {
        right: -15px;
      }
    }
  
    &::before,
    &::after {
      @include pseudo();
      width: 6px;
      height: 2px;
      right: -13px;
      border-radius: 1px;
      background: $strong-color-01;
      transition: .1s;
    }
  
    &::before {
      top: 50%;
      transform: rotate(40deg) translate(-50%, 0);
    }
  
    &::after {
      top: 50%;
      transform: rotate(-40deg) translate(-50%, 0);
    }
  }
}

.link-b {
  @extend .link-a;

  >a {
    &::before,
    &::after {
      display: none;
    }
  }
}
