@use '../../_common/' as *;

p {
  &:not([class]) {
    margin-top: 8px;
  }

  @include rwd(bpSp) {
    font-size: 1.6rem;
  }

  @include rwd(bpSp, below) {
    font-size: 1.4rem;
  }
}

._al-center {
  text-align: center;

  @include rwd(bpSp, below) {
    &._sp-left {
      text-align: left;
    }
  }
}
