@use '../_common/'as *;

.str-wrapper {}

.str-contents-wrapper {
  &._crosstalk {
    >.str-contents-inner {
      >.str-contents {
        max-width: none;
        background: $bright-color-03;
      }
    }
  }

  &._challenge {
    >.str-contents-inner {
      >.str-contents {
        max-width: none;
        padding: 0;
      }
    }
  }

  @include rwd(bpSp, above) {
    &._career {
      .str-contents-inner {
        max-width: 1300px;
        width: 100%;
        margin: 0 auto;

        .str-contents {
          max-width: 980px;
          width: 100%;
          margin: 0 auto;
        }
      }
    }
  }

  @include rwd(bpTab, above) {
    &._crosstalk {
      >.str-contents-inner {
        >.str-contents {
          padding: 80px 70px;
        }
      }
    }
  }

  @include rwd(bpTab, below) {
    &._crosstalk {
      >.str-contents-inner {
        >.str-contents {
          padding: 32px 20px;
        }
      }
    }
  }
}

.str-contents {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;

  >[class^="hdg-"] {
    &:first-child {
      margin-top: 0;
    }
  }

  @include rwd(bpSp, above) {
    padding: 80px 70px 120px;

    ._career & {
      padding: 80px 70px 82px;
    }
  }

  @include rwd(bpSp, below) {
    padding: 48px 20px 56px;

    ._career & {
      padding: 48px 20px 34px;
    }
  }
}

.box-history {
  position: relative;

  &::after {
    @include pseudo();
    background: $bright-color-01;
    border-radius: 1000px;
    border: solid 2px $strong-color-01;
  }

  &.is-animation {
    &::before {
      @include pseudo();
      top: 40px;
      left: -2px;
      width: 2px;
      background: $strong-color-01;
      animation: historyExtendLineAnimation 1s ease-in-out;
    }
  }

  >.hdg {
    position: relative;
    font-family: $font-family-02;
    font-weight: $font-semi-bold;
    color: $strong-color-01;
    padding-bottom: 25px;

    &::before {
      @include pseudo();
      left: -1px;
      background: $strong-color-01;
      border-radius: 1000px;
      transform: translate(-50%, -50%);
    }
  }

  >.list {
    >ol {
      >li {
        position: relative;

        &:nth-of-type(odd) {
          background: $bright-color-03;
        }

        &:nth-of-type(even) {
          background: $bright-color-01;
        }

        >.year {
          position: absolute;
          display: flex;
          justify-content: center;
          align-items: center;
          font-family: $font-family-02;
          font-weight: $font-semi-bold;
          color: $bright-color-01;
          background: $strong-color-01;
          transform: translate(0, -50%);
        }

        >.lyt-history {
          >* {
            &:first-of-type {
              margin-top: 0;
            }
          }

          >.img {
            flex-shrink: 0;
            max-width: 200px;

            >img {
              border-radius: 20px;
              border: solid 1px $bright-color-11;
            }
          }
        }
      }
    }
  }

  @include rwd(bpSp) {
    &.is-animation {
      &::before {
        top: 40px;
        height: calc(100% - 40px);
      }
    }

    &::after {
      width: 32px;
      height: 32px;
      bottom: 0;
      left: -16px;
    }

    >.hdg {
      font-size: 5.6rem;
      padding-left: 47px;

      &::before {
        top: 45px;
        width: 32px;
        height: 32px;
      }
    }

    >.list {
      padding-bottom: 70px;

      >ol {
        >li {
          display: flex;
          align-items: center;
          min-height: 150px;
          padding: 40px 40px 40px 90px;
          border-radius: 0 20px 20px 0;

          >.year {
            width: 104px;
            height: 104px;
            top: 50%;
            left: -53px;
            font-size: 2.4rem;
            border-radius: 1000px;
          }

          >.lyt-history {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;

            >.img {
              margin-left: 40px;
            }
          }
        }
      }
    }
  }

  @include rwd(bpSp, below) {
    &.is-animation {
      &::before {
        top: 30px;
        height: calc(100% - 40px);
      }
    }

    &::after {
      width: 16px;
      height: 16px;
      bottom: 0;
      left: -9px;
    }

    >.hdg {
      font-size: 3.2rem;
      padding-left: 16px;

      &::before {
        top: 25px;
        width: 16px;
        height: 16px;
      }
    }

    >.list {
      >ol {
        >li {
          padding: 44px 20px 40px 20px;

          &:nth-of-type(odd) {
            background: $bright-color-03;
          }

          &:nth-of-type(even) {
            padding: 84px 20px 70px 20px;

            >.year {
              top: 40px;
            }
          }

          >.year {
            width: 100px;
            height: 40px;
            top: 0;
            left: 0;
            font-size: 2rem;
            text-align: center;
            border-radius: 0 20px 20px 0;
          }

          >.lyt-history {
            >.img {
              margin: 32px auto 0;
            }
          }
        }
      }
    }
  }
}

.mv-wrapper {
  position: relative;
  margin: 0 auto;

  &::after {
    @include pseudo();
    background: $bright-color-03;
    border-radius: 1000px;
    z-index: -1;
  }

  .mv-content {
    .carousel-mv {
      position: relative;

      .title {
        @include pseudo();
        top: 50%;
        left: 50%;
        width: 100%;
        color: $bright-color-01;
        text-align: center;
        transform: translate(-50%, -50%);
        z-index: 10;

        .txt-lead {
          display: block;
          font-family: $font-family-01;
          font-weight: $font-medium;
        }
      }

      >ul {
        position: relative;

        >.slide-item {
          position: absolute;
          top: 0;
          left: 0;
          opacity: 0;

          >img {
            filter: brightness(.7);
            border-radius: 30px;
          }
        }

        >.first-slide-item {
          >img {
            filter: brightness(.7);
            border-radius: 30px;
          }
        }
      }
    }
  }

  @include rwd(bpSp) {
    max-width: 1380px;
    width: 100%;
    padding-left: 30px;
    padding-right: 30px;

    &::after {
      bottom: -400px;
      left: -300px;
      width: 782px;
      height: 782px;
    }

    .mv-content {
      .carousel-mv {
        .title {
          .txt-lead {
            max-width: 447px;
            width: 100%;
            margin: 16px auto 0;
          }
        }

        >ul {
          >.first-slide-item {
            position: relative;
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }

  @include rwd(bpSp, below) {
    padding-left: 20px;
    padding-right: 20px;

    &::after {
      bottom: -170px;
      left: -120px;
      width: 255px;
      height: 255px;
    }

    .mv-content {
      .carousel-mv {
        .title {
          .txt-lead {
            max-width: 265px;
            width: 100%;
            margin: 10px auto 0;
          }
        }

        >ul {
          >.first-slide-item {
            >img {
              height: 440px;
              object-fit: cover;
            }
          }

          >.slide-item {
            >img {
              height: 440px;
              object-fit: cover;
            }
          }
        }
      }
    }
  }
}

.interview-mv {
  position: relative;
  border-radius: 30px;
  overflow: hidden;

  >.txt {
    position: relative;
    background: $gradient-color-04;

    >.item {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;

      >.lead {
        color: $bright-color-01;

        >.detail {
          display: block;
        }
      }

      >.name {
        color: $bright-color-01;

        >.detail {
          display: block;
        }
      }
    }
  }

  >.img {
    >img {
      display: block;
      position: absolute;
      top: 50%;
      z-index: -1;
      max-width: none;
      height: auto;
      transform: translate(0, -50%);
      border-radius: 30px;
    }
  }

  @include rwd(bpSp) {
    margin: 0 20px;

    >.txt {
      height: 100%;
      width: 50%;
      border-radius: 30px 0 0 30px;

      >.item {
        padding-left: 48px;
        padding-top: 48px;
        padding-bottom: 48px;
        max-width: 1300px;
        min-height: 480px;

        >.lead {
          color: $bright-color-01;
          font-size: 4rem;

          >.detail {
            display: block;
            font-size: 2.4rem;
          }
        }

        >.name {
          color: $bright-color-01;
          font-size: 2.8rem;

          >.detail {
            display: block;
            font-size: 1.6rem;
          }
        }
      }
    }

    >.img {
      >img {
        right: -260px;
        width: 1287px;
      }
    }
  }

  @include rwd(bpTab) {
    >.img {
      >img {
        width: 1300px;
        right: 0;
      }
    }
  }

  @include rwd(bpSp, below) {
    margin: 0 20px;

    >.txt {
      height: 50%;
      width: 100%;
      border-radius: 0 0 30px 30px;

      >.item {
        padding-left: 20px;
        padding-bottom: 20px;
        max-width: 1300px;
        min-height: 480px;
      }
    }

    >.img {
      >img {
        right: -260px;
        width: 1287px;
      }
    }
  }

  @include rwd(bpTab, below) {
    >.txt {
      >.item {
        >.lead {
          font-size: 2.4rem;

          >.detail {
            font-size: 1.6rem;
          }
        }

        >.name {
          font-size: 2rem;

          >.detail {
            font-size: 1.4rem;
          }
        }
      }
    }
  }
}

.crosstalk-mv-wrapper {
  @include rwd(bpSp, above) {
    max-width: 1430px;
    margin: 0 auto;
    padding: 0 70px;
  }

  @include rwd(bpSp, below) {
    padding: 0 20px;
  }
}

.crosstalk-mv {
  position: relative;

  >.txt {
    >.txt-special-a {
      margin: 0 auto;
    }

    >.item {
      >p {
        text-align: center;
      }
    }
  }

  @include rwd(bpSp, above) {
    overflow: hidden;
    border-radius: 30px;

    >.img {
      img {
        width: auto;
      }
    }

    >.txt {
      position: absolute;
      top: 40px;
      right: 40px;
      padding: 50px 60px;
      border-radius: 20px;
      background: $bright-color-01;

      >.item {
        >p {
          margin-top: 17px;
        }
      }
    }
  }

  @include rwd(bpSp, below) {
    >.img {
      overflow: hidden;
      border-radius: 30px;
    }

    >.txt {
      margin-top: 40px;

      >.item {
        >p {
          margin-top: 25px;
        }
      }
    }
  }
}

.challenge-mv {
  position: relative;
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 20px;

  >.img {
    height: 100%;
    border-radius: 30px;
    overflow: hidden;

    img {
      width: auto;
    }
  }

  >.txt {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    font-weight: $font-bold;

    >p {
      color: $bright-color-01;
      margin-top: 0;

      >.detail {
        display: block;
        margin-top: 5px;
        font-family: $font-family-02;
        font-weight: $font-regular;
      }
    }
  }

  @include rwd(bpSp, above) {
    >.txt {
      >p {
        font-size: 4rem;

        >.detail {
          font-size: 2rem;
        }
      }
    }
  }

  @include rwd(424px, below) {
    >.img {
      img {
        width: 383px;
      }
    }
  }

  @include rwd(bpSp, below) {
    height: 150px;

    >.txt {
      >p {
        font-size: 2.4rem;

        >.detail {
          font-size: 1.4rem;
        }
      }
    }
  }
}
