@use '../../_common/' as *;

.box-a {
  height: 100%;
  border-radius: 20px;
  border: solid 1px $bright-color-11;

  >* {
    &:first-of-type {
      margin-top: 0;
    }
  }

  @include rwd(bpSp) {
    padding: 24px 24px 32px 32px;
  }

  @include rwd(bpSp, below) {
    padding: 20px;
  }
}

.box-faq-a {
  margin-right: calc(50% - 50vw);
  margin-left: calc(50% - 50vw);
  background-color: $bright-color-03;

  @include rwd(bpSp) {
    margin-right: 0;
    margin-left: 0;
    border-radius: 20px;
  }

  .inner {
    padding: 16px 20px 32px;

    @include rwd(bpSp) {
      padding: 32px;
    }

    >.list {
      >.item {
        margin-top: 16px;

        &:first-of-type {
          margin-top: 0;
        }

        [data-script-enabled="true"] & {
          >dt {
            >a {
              border-radius: 10px;
            }
          }

          >dd {
            display: none;
            overflow: hidden;
            transition: .3s ease-in-out;

            &::before {
              display: none;
            }
          }
        }

        &.is-active {
          >dt {
            >a {
              border-radius: 10px 10px 0 0;

              >.item-inner::after {
                transform: translateY(-40%) rotate(90deg);
              }
            }
          }

          >dd {
            display: flex;

            &::before {
              display: block;
            }

            >.mark-a {
              display: flex;
            }
          }
        }

        >dt {
          >a {
            display: block;
            padding: 16px 46px 13px 22px;
            margin-left: 12px;
            background-color: $bright-color-01;
            border-radius: 10px 10px 0 0;
            color: ;
            text-decoration: none;

            @include rwd(bpSp) {
              padding: 16px 3em 16px 16px;
              font-size: 1.6rem;
              margin-left: 0;
            }

            >.item-inner {
              position: relative;
              display: flex;
              align-items: center;
              font-size: 1.4rem;

              >.mark-q {
                position: absolute;
                display: flex;
                align-items: center;
                justify-content: center;
                top: 50%;
                left: -34px;
                transform: translateY(-50%);
                width: 24px;
                height: 24px;
                border-radius: 5px;
                background-color: $strong-color-01;
                text-align: center;
                color: $bright-color-01;
                font-size: 1.2rem;
                font-weight: $font-bold;

                @include rwd(bpSp) {
                  position: static;
                  width: 40px;
                  height: 40px;
                  margin-right: 16px;
                  border-radius: 10px;
                  font-size: 1.6rem;
                  transform: none;
                }
              }

              &::before,
              &::after {
                @include pseudo();
                border-radius: 1px;
                background: $gradient-color-01
              }

              &::before {
                top: 50%;
                right: -30px;
                width: 14px;
                height: 2px;

                @include rwd(bpSp) {
                  right: -20px;
                }
              }

              &::after {
                top: 50%;
                right: -24px;
                width: 2px;
                height: 14px;
                transform: translateY(-40%);
                transition: transform 0.3s;

                @include rwd(bpSp) {
                  right: -14px;
                }
              }

              >p {
                flex: 1;
                margin-top: 0;

                @include rwd(bpSp) {
                  font-size: 1.6rem;
                }
              }
            }
          }
        }

        >dd {
          position: relative;
          border-radius: 0 0 10px 10px;

          @include rwd(bpSp, below) {
            padding-left: 12px;
          }

          >.mark-a {
            position: absolute;
            display: flex;
            align-items: center;
            align-self:flex-start;
            justify-content: center;
            flex-shrink: 0;
            width: 24px;
            height: 24px;
            border-radius: 5px;
            border: 1px solid $strong-color-01;
            background-color: $bright-color-01;
            text-align: center;
            color: $strong-color-01;
            font-size: 1.2rem;
            font-weight: $font-bold;

            @include rwd(bpSp) {
              top: 16px;
              left: 16px;
              width: 40px;
              height: 40px;
              border-radius: 10px;
              font-size: 1.6rem;
              transform: none;
            }

            @include rwd(bpSp, below) {
              top: 10px;
              left: 0;
            }
          }

          >div {
            width: 100%;
            background: $bright-color-01;
            border-radius: 0 0 10px 10px;

            @include rwd(bpSp) {
              margin-left: 0;
              border-top: none;
              font-size: 1.6rem;

              &::before {
                @include pseudo();
                top: 0;
                left: 16px;
                width: calc(100% - 32px);
                height: 1px;
                border-top: 1px dashed $bright-color-11;
              }
            }

            @include rwd(bpSp, below) {
              border-top: 2px solid $bright-color-03;
            }

            >.item-inner {
              font-size: 1.4rem;

              @include rwd(bpSp) {
                padding: 16px 16px 16px 70px;
              }

              @include rwd(bpSp, below) {
                padding: 10px 16px 16px 22px;
              }

              >p {
                flex: 1;

                @include rwd(bpSp) {
                  font-size: 1.6rem;
                }
              }

              >*:first-of-type {
                  margin-top: 0;
              }
            }
          }
        }
      }
    }
  }
}

.box-message {
  position: relative;
  overflow: hidden;

  &::before {
    @include pseudo();
    background: url('/docs/company/recruit/graduates/common/images/decoration-circle-01.svg') center / cover;
    z-index: -1;
  }

  &::after {
    @include pseudo();
    border: solid 3px $strong-color-01;
    border-radius: 1000px;
    z-index: -1;
  }

  @include rwd(bpSp) {
    &::before {
      top: 130px;
      right: -290px;
      width: 538px;
      height: 538px;
    }

    &::after {
      bottom: -170px;
      left: -203px;
      width: 373px;
      height: 373px;
    }
  }

  @include rwd(bpSp, below) {
    padding: 56px 20px 93px;

    &::before {
      top: 230px;
      right: -150px;
      width: 200px;
      height: 200px;
    }

    &::after {
      bottom: -100px;
      left: -130px;
      width: 182px;
      height: 182px;
    }
  }
}

.box-message-inner {
  p {
    margin-top: 0;
    font-weight: $font-medium;
  }

  @include rwd(bpSp) {
    max-width: 580px;
    width: 100%;
    margin: 120px auto 140px;

    .hdg-top-inner {
      + p {
        margin-top: 64px;
      }
    }

    p {
      font-size: 1.8rem;

      + p {
        margin-top: 50px;
      }
    }
  }

  @include rwd(bpSp, below) {
    .hdg-top-inner {
      + p {
        margin-top: 56px;
      }
    }

    p {
      font-size: 1.4rem;
  
      + p {
        margin-top: 32px;
      }
    }
  }
}

.box-about {
  @include rwd(bpSp) {
    .box-about-img {
      img {
        max-width: 1440px;
        width: calc(100% - 30px);
        border-radius: 0 20px 20px 0;
      }
    }

    .box-about-inner {
      max-width: 1440px;
      width: 100%;
      margin: 0 auto;
      padding: 0 70px;
    }
  }

  @include rwd(bpSp, below) {
    position: relative;

    .box-about-img {
      img {
        width: calc(100% - 20px);
      }
    }

    .box-about-inner {
      padding: 36px 20px 0;
    }
  }
}

.box-challenge {
  position: relative;
  overflow: hidden;

  .box-challenge-inner {
    background: $gradient-color-02;
  }

  @include rwd(bpTab) {
    padding-top: 176px;

    .box-challenge-inner {
      display: flex;
      align-items: center;
      padding-right: 70px;

      >.img-item {
        position: relative;
        width: 100%;
        max-width: 573px;
        min-height: 523px;
        overflow: hidden;

        >img {
          position: absolute;
          top: 0;
          left: 0;
        }
      }

      >.txt-item {
        padding: 120px 0 50px 80px;
      }
    }
  }

  @include rwd(bpTab, below) {
    padding-top: 95px;

    .box-challenge-inner {
      >.img-item {
        max-width: 573px;
        width: 100%;
      }

      >.txt-item {
        padding: 43px 20px 40px;
      }
    }
  }
}

.box-person {
  position: relative;
  overflow: hidden;

  &::before {
    @include pseudo();
    border: solid 3px $strong-color-01;
    border-radius: 1000px;
    z-index: -1;
  }

  @include rwd(bpSp) {
    padding-top: 120px;

    &::before {
      top: -172px;
      right: -203px;
      width: 373px;
      height: 373px;
    }

    .box-person-inner {
      max-width: 1440px;
      width: 100%;
      margin: 0 auto;
      padding: 80px 70px 0;

      >.hdg-f {
        + p {
          max-width: 660px;
          margin-top: 32px;
        }
      }
    }
  }

  @include rwd(bpSp, below) {
    padding-top: 64px;

    &::before {
      top: -100px;
      right: -100px;
      width: 200px;
      height: 200px;
    }

    .box-person-inner {
      padding: 42px 20px 0;
    }
  }
}

.box-career {
  position: relative;

  @include rwd(1024px) {
    margin-top: 195px;

    .box-career-inner {
      position: relative;
      overflow: hidden;
      margin-bottom: 120px;

    .box-career-img {
      img {
        position: absolute;
        top: 50%;
        right: 0;
        z-index: -1;
        max-width: none;
        width: 1700px;
        height: auto;
        transform: translate(0, -50%);
      }
    }

      .box-career-txt {
        max-width: 1410px;
        min-height: 637px;
        margin: 0 0 0 auto;
        padding-left: 70px;
        padding-top: 130px;

        >.item {
          width: 70%;
          margin: 0 0 0 auto;
          padding: 80px;
          background: rgba(255, 255, 255, .8);
          border-radius: 30px 0 0 30px;
        }
      }
    }
  }

  @include rwd(1024px, below) {
    margin-top: 95px;
    margin-bottom: 56px;

    .box-career-inner {
      .box-career-txt {
        margin-top: 42px;
        padding: 0 20px;
      }

      .box-career-img {
        img {
          margin: 0 0 0 auto;
          width: calc(100% - 20px);
          border-radius: 20px 0 0 20px;
        }
      }
    }
  }

  @include rwd(1440px) {
    .box-career-inner {
      .box-career-img {
        img {
          width: calc(100% - 20px);
          border-radius: 20px 0 0 20px;
        }
      }
    }
  }
}

.box-recommend {
  @include rwd(bpSp) {
    margin-bottom: 80px;
    padding-top: 82px;
  }

  @include rwd(bpSp, below) {
    margin-bottom: 58px;
  }
}

.box-career-a {
  >.ttl {
    color: $strong-color-01;
    font-family: $font-family-02;
  }

  @include rwd(bpSp) {
    display: flex;

    >.ttl {
      display: flex;
      align-items: center;
      font-size: 4rem;
      margin-right: 40px;
    }

    >.content {
      position: relative;
      margin-left: 40px;

      >* {
        &:first-child {
          margin-top: 0;
        }
      }

      &::before {
        @include pseudo;
        top: 0;
        left: -40px;
        width: 1px;
        height: 100%;
        background: $bright-color-09;
      }
    }
  }

  @include rwd(bpSp, below) {
    >.ttl {
      font-size: 3.2rem;
      margin-bottom: 16px;
      text-align: center;
    }

    >.content {
      position: relative;
      margin-top: 16px;

      &::before {
        @include pseudo;
        top: -16px;
        left: 0;
        width: 100%;
        height: 1px;
        background: $bright-color-09;
      }
    }
  }
}

.box-career-b {
  position: relative;
  width: 100%;
  border: solid 1px $strong-color-01;
  border-radius: 20px;

  &::before,
  &::after {
    @include pseudo();
    left: 0;
    width: 100%;
    height: 1px;
    background: $bright-color-09;
  }

  >.txt-content {
    background: $bright-color-03;
    border-radius: 20px 20px 0 0;

    >.ttl {
      font-weight: $font-bold;
      text-align: center;
    }
  }

  >.btn-content {
    >a {
      position: relative;
      display: block;
      color: ;
      text-align: center;
      text-decoration: none;

      @include hover() {
        >.arrow {
          &::before {
            transition: transform .4s cubic-bezier(.23, 1, .32, 1) 0s, opacity .3s cubic-bezier(.23, 1, .32, 1) 0s;
            opacity: 0;
            transform: translateX(300%);
          }

          &::after {
            transition: transform .4s cubic-bezier(.23, 1, .32, 1) .1s;
            transform: translateX(0);
          }
        }
      }

      >.arrow {
        position: absolute;
        top: 50%;
        display: inline-block;
        width: 32px;
        height: 32px;
        border-radius: 1000px;
        background: $gradient-color-01;
        overflow: hidden;
        transform: translate(0, -50%);

        &::before,
        &::after {
          @include pseudo();
          top: 14px;
          right: 12px;
          width: 8px;
          height: 5px;
          background: url('/docs/company/recruit/graduates/common/images/icon-arrow-03.svg') center/cover;
        }

        &::after {
          transform: translateX(-300%);
        }
      }
    }
  }

  @include rwd(bpSp, above) {
    margin-top: 160px;
    margin-bottom: 80px;

    &::before {
      top: -80px;
    }

    &::after {
      bottom: -80px;
    }

    >.txt-content {
      padding: 50px 20px;

      >.ttl {
        font-size: 2.4rem;
      }
    }

    >.btn-content {
      >a {
        padding: 30px 80px 30px 80px;

        >.arrow {
          right: 24px;
        }
      }
    }
  }

  @include rwd(bpSp, below) {
    margin-top: 65px;
    margin-bottom: 32px;

    &::before {
      top: -32px;
    }

    &::after {
      bottom: -32px;
    }

    >.txt-content {
      padding: 33px 32px;

      >.ttl {
        font-size: 2rem;
      }
    }

    >.btn-content {
      >a {
        padding: 23px 64px 23px 16px;

        >.arrow {
          right: 16px;
        }
      }
    }
  }
}

.box-carousel-a {
  padding: 0 20px;
  margin-bottom: 80px;
}

.box-member-a {
  @include rwd(bpSp, above) {
    max-width: 1430px;
    margin: 0 auto;
    padding: 80px 70px;
  }

  @include rwd(bpSp, below) {
    padding: 50px 20px 32px;
  }
}

.box-crosstalk-a {
  position: relative;

  &::before {
    @include pseudo();
    bottom: 0;
    left: 0;
    width: 100%;
    background: $bright-color-03;
    z-index: -1;
  }

  >.link {
    >a {
      position: relative;
      display: block;
      max-width: 1120px;
      width: 100%;
      margin: 0 auto;
      text-decoration: none;

      >.img {
        width: 100%;
        border-radius: 20px;
        overflow: hidden;

        >img {
          width: auto;
        }
      }

      >.txt-content {
        >.txt {
          position: relative;

          &::before,
          &::after {
            @include pseudo();
            width: 20px;
            height: 4px;
            top: 50%;
            right: 50%;
            background: $gradient-color-01;
            border-radius: 1.5px;
            transition: .3s;
          }

          &::before {
            transform: rotate(40deg);
            top: 48%;
            right: 0;
          }

          &::after {
            transform: rotate(-400deg);
            top: 62%;
            right: 0;
          }

          >.ttl {
            font-weight: $font-semi-bold;
            font-family: $font-family-02;
            color: $strong-color-01;

            >.detail {
              display: block;
              color: ;
              font-family: $font-family-01;
            }
          }
        }
      }
    }
  }

  @include rwd(bpSp, above) {
    padding: 0 20px 40px;

    &::before {
      height: 240px;
    }

    >.link {
      >a {
        @include hover {
          >.txt-content {
            >.txt {
              &::before,
              &::after {
                right: -5px;
              }
            }
          }
        }

        >.img {
          position: relative;

          &::before {
            @include pseudo();
            width: 70%;
            height: 100%;
            top: 0;
            right: 0;
            background: $gradient-color-05;
          }
        }

        >.txt-content {
          position: absolute;
          top: 0;
          left: 0;
          display: flex;
          justify-content: flex-end;
          align-items: center;
          width: 100%;
          height: 100%;
          padding-right: 40px;

          >.txt {
            padding-right: 96px;

            >.ttl {
              font-size: 4rem;

              >.detail {
                font-size: 1.6rem;
              }
            }
          }
        }
      }
    }

    +.box-recommend {
      margin-top: 82px;
    }
  }

  @include rwd(bpSp, below) {
    padding: 0 20px 30px;

    &::before {
      height: 290px;
    }

    >.link {
      >a {
        >.txt-content {
          margin-top: 24px;
          >.txt {
            >.ttl {
              font-size: 3.2rem;

              >.detail {
                font-size: 1.6rem;
              }
            }
          }
        }
      }
    }

    +.box-recommend {
      margin-top: 58px;
    }
  }
}

.box-crosstalk-b {
  position: relative;

  >.txt {
    border-radius: 20px;
    background: $bright-color-01;

    >.hdg {
      border-bottom: solid 1px $strong-color-01;

