      >.txt {
        border-radius: 0 20px 20px 0;
      }
    }

    &._right {
      margin-right: -20px;

      >.txt {
        border-radius: 20px 0 0 20px;
      }
    }

    >.txt {
      padding: 32px 20px 182px;
      margin-bottom: -160px;

      >.hdg {
        font-size: 2rem;
        padding-bottom: 17px;

        >.detail {
          font-size: 1.8rem;
        }
      }

      >.talk-item {
        margin-top: 24px;

        >.img {
          width: 56px;

          >.name {
            font-size: 1.4rem;
          }
        }

        >.txt {
          margin-left: 16px;
        }
      }
    }

    >.img {
      max-width: 408px;
      margin: 0 auto;
      padding: 0 20px;
    }
  }
}